<script>
/*****************************************
 * Module: OG-Editor
 * Description: The editor provides a holder for primitive types (inputs, dropdowns, etc)
 *      that will be wrapped either within a modal (og-modal) or a md-card. It also creates
 *      a validation wrapper to be used on inputs added to the editor. 
 * 
 *      This also has the ability to have the editor in edit mode. Which means you can have
 *      an edit button and toggle between the edit and view only modes. Along with this there
 *      is the ability to apply system permissions to the whole editor. If the user does
 *      not have appropriate permissions the fields and buttons will be in view only mode.
 * 
 * Optional Properties:
 *      name: The name of the editor. Should be a unique value for what is displayed on 
 *          the screen.
 *      title: The title of the modal or card
 *      subhead: The sub header to accompany a title. Will only show when a title is set.
 *      meta: The content of this field will be shown in the lower left corner of the 
 *          modal or card.
 *      modal: Adding modal as an attribute or setting the modal attribute to true will have the
 *          editor enabled in a modal when engaged.
 *      show: Used when modal option is used to toggle between and open and closed modal state.
 *      editMode: Added as an attribute or an attribute with a value of true will add the ability
 *          to edit or view only version of the editor. When defined the default state will be
 *          for the editor to be in view state with an edit button on the screen.
 *      editPermission: This is a single string value of the permission allowed to view/edit the 
 *          editor that is checked against the System-Permission service.
*****************************************/
</script>
<template>
    <og-modal v-if="modal" :show="show" size="large" :title="title" @close="close" :name="name" :subhead="subhead">
        <slot :validator="validator"></slot>
        <og-block slot="footer" layout="50 25 25">
            <span>{{meta}}</span>
            
            <span v-if="!showSave"></span>
            <og-button icon="edit" v-if="!showSave" :disabled="!allowed" @click="engageEdit">Edit</og-button>
            
            <og-button icon="save" v-if="showSave" :disabled="!allowed" @click="save">Save</og-button>
            <og-button icon="close" v-if="showSave" :disabled="!allowed" @click="close">Cancel</og-button>
        </og-block>
    </og-modal>
    <div v-else-if="!modal">
        <md-card md-with-hover>
            <md-card-header v-if="title">
                <div class="md-title">{{ title }}</div>
                <div class="md-subhead" v-if="subhead">{{ subhead }}</div>
            </md-card-header>
            <md-card-content>
                <slot :validator="validator"></slot>
            </md-card-content>
            <md-card-actions>
                <og-block layout="50 25 25">
                    <span>{{meta}}</span>
                    
                    <span v-if="!showSave"></span>
                    <og-button icon="edit" v-if="!showSave" :disabled="!allowed" @click="engageEdit">Edit</og-button>
                    
                    <og-button icon="save" v-if="showSave" :disabled="!allowed" @click="save">Save</og-button>
                    <og-button icon="close" v-if="showSave" :disabled="!allowed" @click="close">Cancel</og-button>
                </og-block>
            </md-card-actions>
        </md-card>
    </div>
</template>

<script>
    import permissionsEdit from '../mixins/permission-edit'
    import Validator from '../../services/validator';

    export default {
        data() {
            return {
                editModeState: true,
                originalValues: {}
            }
        },
        mixins: [permissionsEdit],
        props: {
            show: {
                type: Boolean,
                default: false
            },
            modal: {
                type: Boolean,
                default: false
            },
            title: {
                type: String
            },
            subhead: {
                type: String
            },
            meta: {
                type: String
            },
            name: {
                type: String,
                default: 'dialog1'
            },
            editMode: {
                type: Boolean,
                default: false
            },
            editPermission: {
                type: String
            }
        },
        computed: {
            allowed () {
                return this._canEditPermission === undefined || (this._canEditPermission !== undefined && this._canEditPermissionResult)
            },
            showSave () {
                return this.editModeState && this.allowed
            }
        },
        created() {
            if (this.editMode) {
                this.editModeState = false
                this.$root.$emit('edit-mode-enabled', false)
            }

            this.validator = new Validator();
        },
        methods: {
            save() {
                this.validator.validateAll().then((result) => {
                    if(result.length == result.filter(x => x).length){
                        this.$emit('save');
                        
                        if (this.editMode) {
                            this.editModeState = false
                            this.$root.$emit('edit-mode-update', this.editModeState)
                        }
                    }
                });
            },
            reset() {
                this.$emit('reset');
                this.validator.clear();
            },
            close() {
                if (this.editMode) {
                    this.editModeState = false
                    this.$root.$emit('edit-mode-update', this.editModeState)
                }
                
                this.$emit('close');
                this.reset();
            },
            engageEdit () {
                this.editModeState = true
                this.$root.$emit('edit-mode-update', this.editModeState)
            }
        }
    }

</script>

<style lang="scss">
    .md-gutter:not(.md-column) {
        margin-right: -6px;
        margin-left: -6px;
    }

    .md-card {
        margin: 20px;
    }
</style>


