<template>
    <md-list md-dense>
      <og-tree-item class="item" :model="model" :name="name" :children="children" :url="url" :target="target" :selectedItem="selectedItem" @item-click="itemClick" :key="guid()"></og-tree-item>
    </md-list>
</template>

<script>
  import utilities from '../../../utilities'

  export default {
    name: 'og-tree',
    data() {
      return {
        selectedItem: undefined
      }
    },
    props: {
      model: {
        type: Object
      },
      name: {
        type: Function,
        default: function(item){
          return item.name;
        }
      },
      children: {
        type: Function,
        default: function(item){
          return item.children;
        }
      },
      url: {
        type: Function,
        default: function(item){
          return item.url;
        }
      },
      target: {
        type: Function,
        default: function(item){
          return item.target;
        }
      }
    },
    methods: {
      itemClick(item){
        this.selectedItem = item;
        this.$emit('item-click', item);
      },
      guid(){
        return utilities.guid();
      }
    }
  }
</script>

<style scoped>
  ul {
    padding-left: 1em;
    line-height: 1.5em;
    list-style-type: dot;
  }
</style>


