import Vue from 'vue'
import { storiesOf } from '@storybook/vue'

storiesOf('Switch', module)
  .add('default',
    () => ({
    template: '<og-switch label="Switch Example" v-model="selected"></og-switch>',
    data: () => ({
      selected: true
    })
  }))
  .add('off',
    () => ({
    template: '<og-switch label="Switch Example" v-model="selected"></og-switch>',
    data: () => ({
      selected: false
    })
  }))


