<template>
    <md-table class="compare">
        <md-table-header>
            <md-table-row>
                <md-table-cell>Property</md-table-cell>
                <md-table-cell>Source</md-table-cell>
                <md-table-cell>Target</md-table-cell>
            </md-table-row>
        </md-table-header>
        <md-table-body>
            <md-table-row v-for="(property, key, index) in mergedObjectKeys" :key="key">
                <md-table-cell>{{property}}</md-table-cell>
                <md-table-cell>{{source && source[property]}}</md-table-cell>
                <md-table-cell :class="{'different': (sourceCompare && sourceCompare[property]) !== (targetCompare && targetCompare[property])}">{{target && target[property]}}</md-table-cell>
            </md-table-row>
        </md-table-body>
    </md-table>
</template>

<script>
export default {
  name: 'og-compare',
  props: ['source','target','source-compare','target-compare'],
  computed: {
    mergedObjectKeys(){
        if(!this.source && !this.target){
            return [];
        }

        return Object.keys(this.source || {}).concat(Object.keys(this.target || {}))
        .distinct()
        .filter(i => i != 'RecordCount' && !i.startsWith('__') && i != 'AttributeType');
    }
  }
}
</script>

<style lang="scss" scoped>
.compare .md-table-cell {
    width: 40%;
}

.compare .md-table-cell:first-child {
    width: 20%;
}

.compare .md-table-cell.different {
    background-color: #fafad2;
}
</style>


