<template>
    <div>
        <h3 v-if="title">{{title}}</h3>
        <md-table v-if="show">
            <md-table-body>
                <md-table-row v-for="(property, key, index) in safeObjectKeys(source)" :key="key">
                    <md-table-cell>{{property}}</md-table-cell>
                    <md-table-cell>{{source[property]}}</md-table-cell>
                </md-table-row>
            </md-table-body>
        </md-table>
        <span v-else>None.</span>
    </div>
</template>

<script>
export default {
  name: 'og-property-grid',
  props: ['source','title'],
  computed: {
    show(){
        return this.safeObjectKeys(this.source).length > 0;
    }
  },
  methods: {
    safeObjectKeys(obj){
        if(!obj){
            return [];
        }

        return Object.keys(obj).filter(i => i != 'RecordCount' && !i.startsWith('__') && i != 'AttributeType');
    }
  }
}
</script>

<style lang="scss" scoped>
</style>


