    _parseFieldDefinitionToJSON(params) {
        return this.trackFileDefinition(params)
            .then(res => {
                var definitionDetails = res.data
                var fieldDefDetails = []

                definitionDetails.forEach(function (fieldDef) {
                    var _fieldDef = {}
                    var propertyNames = Object.getOwnPropertyNames(fieldDef)
                
                    var fieldDefPropNames = propertyNames.filter(f => { return f.indexOf("FieldDefinition") >= 0 })
                    fieldDefPropNames.forEach(propName => {
                        if (propName === 'FieldDefinition_Priority') {
                            _fieldDef[propName.split('_')[1]] = parseInt(fieldDef[propName] || 0)
                        }
                        else {
                            _fieldDef[propName.split('_')[1]] = fieldDef[propName]
                        }
                    })

                    var _controlDef = {}
                    if (_fieldDef.ControlDefinitionID) {
                        var propertyNames = propertyNames.filter(f => { return f.indexOf("ControlDefinition") >= 0 })

                        propertyNames.forEach(propName => {
                            _controlDef[propName.split('_')[1]] = fieldDef[propName]
                        })

                        _fieldDef.ControlDefinition = _controlDef
                    }

                    fieldDefDetails.push(_fieldDef)
                })

                return fieldDefDetails
            })
    }

