<template>
    <og-datepicker
        :name="name"
        :label="label"
        v-model="inputValue"
        :inputType="inputType"
        :pattern="pattern"
        :title="title"
        @input="update">
    </og-datepicker>
</template>

<script>
    import mixin from './mixin.js'

    export default {
        name: "og-field-datepicker",
        mixins: [ mixin ],
    }
</script>

<style lang="scss">

</style>



