<template>
  <md-input-container v-bind:class="{'md-input-invalid': error}" class="og-text" v-show="showInput" @focus.native="$refs.text.$el.focus()">
    <md-icon class="og-text-icon" v-if="icon">{{ icon }}</md-icon>
    <label class="og-text-label" :for="name">{{label}}</label>
    <span class="og-text-span-input">
      <md-input class="og-text-input" :name="name" ref="text" :readonly="readonly" v-model="inputValue" @input="input($event)" :disabled="!enableInput" :type="inputType" :patten="pattern"></md-input>
    </span>
    <md-button class="md-icon-button" v-show="buttonEnabled" @click.native="buttonClicked">
      <md-icon>{{buttonIcon}}</md-icon>
    </md-button>
    <span v-show="error" class="md-error"><og-icon v-show="error" icon="error" class="md-accent"></og-icon> {{ error }}</span>
  </md-input-container>
</template>

<script>
import input from '../mixins/input'
import permissionsEdit from '../mixins/permission-edit'
import permissionsView from '../mixins/permission-view'

export default {
  name: 'og-text',
  mixins: [input, permissionsEdit, permissionsView],
  props: {
    buttonEnabled: {
      type: Boolean,
      default: false
    },
    buttonIcon: {
      type: String,
      default: undefined
    },
    icon: {
      type: String,
      default: undefined
    },
    inputType: {
      type: String,
      default: 'text'
    },
    pattern: {
      type: RegExp
    },
    readonly: {
      type: Boolean,
      default: false
    }
  },
  methods: {
    buttonClicked () {
      this.$emit('buttonClicked')
    }
  }
}
</script>

<style lang="scss" scoped>
  .og-text.md-input-container {
    &:after {
      background-color: transparent;
    }

    .md-icon.og-text-icon {
      margin-left: 8px;
    }

    span.og-text-span-input {
      position: absolute;
      top: 15px;
      width: 100%;
      
      &:after {
        background-color: rgba(0,0,0,.12);
        bottom: 0;
        content: " ";
        height: 1px;
        position: absolute;
        right: 0;
        width: 100%;
      }
    }

    .md-icon.og-text-icon ~ span.og-text-span-input {
      left: 36px;
    }

    &.md-input-focused {
      span.og-text-span-input:after {
        height:2px;
        text-shadow: 0 0 0 rgba(0,0,0,1);
      }
    }

    .md-icon-button {
      margin: 0;
      position: absolute;
      right: 0;
      bottom: -2px;
      color: rgba(0,0,0,.38);
    }
  }
</style>


