<template>
    <!-- Please keep the anchor tag and its content on a single line -->
    <a :href="href" @click="click($event)" :target="target"><og-icon v-if="icon" :icon="icon"></og-icon><slot></slot></a>
</template>

<script>
  export default {
    props: {
      href: {
				type: String,
				default: '#'
			},
      icon: {
        type: String,
				default: undefined
      },
      target: {
        type: String,
        default: undefined
      }
    },
    methods: {
      click(e){
        this.$emit('click');
        e.stopPropagation();
      }
    }
  }
</script>

<style scoped>
  a {
    text-decoration: none;
  }
  a:hover {
    text-decoration: underline;
  }
</style>


