<template>
<section class="og-section">
	<h1 v-if="title">{{title}}</h1>
	<slot>Your slot seems lonely. Put something in it!</slot>
</section>
</template>

<script>
export default {
  name: 'og-section',
  props: ['title']
}
</script>

<style lang="scss" scoped>
.og-section {
    background-color: white;
    padding: 40px;
}
</style>


