<template>
    <og-text 
        :name="name"
        :label="label"
        v-model="inputValue"
        :icon="icon"
        :inputType="inputType"
        :pattern="pattern"
        :title="title"
        @input="update"></og-text>
</template>

<script>
    import mixin from './mixin.js'

    export default {
        name: "og-field-textbox",
        mixins: [ mixin ],
        data () {
            return {
                icon: undefined,
                inputType: "text",
            }
        },
        created() {
            var controlType = (this.fieldDefinition.ControlType || 'default').toLowerCase()
            
            if ('email' === controlType) this.inputType = 'email'
            if ('hidden' === controlType) this.inputType = 'hidden'
            
            if (this.fieldDefinition.DataType && this.fieldDefinition.DataType.toLowerCase() != 'string') {
                this.inputType = this.fieldDefinition.DataType.toLowerCase()
            }
            
            if (this.fieldDefinition.DataType && !this.fieldDefinition.ValidationRegex) {
                if (this.fieldDefinition.DataType.toLowerCase() === 'decimal') {
                    this.addValidation("regex:/\\d+(\\.\\d{0,5})?/")
                    this.title = 'Decimal Value Only'
                } else if (this.fieldDefinition.DataType.toLowerCase() === 'int') {
                    this.addValidation("regex:/[0-9]{1,10}/")
                    this.title = 'Integer Value Only'
                    this.inputType = 'number'
                }
            }

            if (this.fieldDefinition.DataFormat && this.fieldDefinition.DataFormat.toLowerCase() === 'phone') {
                this.addValidation("regex:/((?:[^a-zA-z]*\\d){10})($|\\s?x\s?(?:[^a-zA-z]*\\d){1,10}?)$/")
                this.title = 'Invalid Phone - 999-999-9999 or 999-999-9999x999'
                this.inputType = 'tel'
            }

            if (this.fieldDefinition.CssClass === 'Money') {
                this.icon = 'attach_money'
            }

            if (this.fieldDefinition.CssClass === 'Percent') {
                this.icon = '%' // replace if material icon creates a percent icon
            }

            if (this.fieldDefinition.MinLength) {
                this.addValidation('min:' + this.fieldDefinition.MinLength)
            }

            if (this.fieldDefinition.MaxLength) {
                this.addValidation('max:' + this.fieldDefinition.MaxLength)
            }
        }
    }
</script>

<style lang="scss">

</style>



