import Vue from 'vue'
import { storiesOf } from '@storybook/vue'

storiesOf('Router Link', module)
  .add('default',
    () => ({
    template: '<og-section title="Notes">The router link is currently not supported in the storybook. The purpose of the component is to link to other pages known to vue. When used, as opposed to regular links it will move to the new page like a SPA (single page application) without a refresh and seamlessly. Until storybook support is added/configured please as Haney or Chavez if you need assistance.</og-section>',
  }))


