import og from '../../services/og'

export default {
    data() {
        return {
            showInput: true,
            _canViewPermission: undefined
        }
    },
    props: {
        canViewPermission: {
            type: String
        }
    },
    created() {
        this._trySetViewPermissions()
    },
    methods: {
        _trySetViewPermissions() {
            if (this.canViewPermission) this._canViewPermission = this.canViewPermission
            if (this.canViewPermission === undefined) {
                var parent = this.$parent

                while (parent && !parent.canViewPermission) {   
                    if (!parent.$parent) break

                    parent = parent.$parent
                }

                this._canViewPermission = parent.canViewPermission
            }

            if (this._canViewPermission) {
                og.security.systemPermission.userHasPermission(this._canViewPermission, res => {
                    this.showInput = res
                })
            }
        }
    }
}


