<template>
    <div v-show="showInput">
        <md-radio
            @change="input($event)" 
            v-model="inputValue"
            :disabled="!enableInput"
            v-for="(option, key, index) in options"
            :md-value="option.value"
            :key="key"
            :name="name"
            :id="getUniqueName(name, key)">
            {{ option.text }}
        </md-radio>
    </div>
</template>

<script>
    import input from '../mixins/input'
    import permissionsEdit from '../mixins/permission-edit'
    import permissionsView from '../mixins/permission-view'
    
    export default {
        "name": "og-radio",
        mixins: [input, permissionsEdit, permissionsView],
        props: ['options'],
        methods: {
            getUniqueName(name, key) {
                return name + "_" + key
            }
        }
    }
</script>

<style lang="scss">

</style>



