<template>
    <og-dropdown
        :options="options"
        :name="name"
        :label="label"
        v-model="inputValue"
        :title="title"
        @input="update"></og-dropdown>
</template>

<script>
    import mixin from './mixin.js'

    export default {
        name: "og-field-dropdown",
        mixins: [ mixin ],
        data () {
            return {
                options: []
            }
        },
        created () {
            this.options = this.fieldDefinition.SelectOptions.split('\n').map(x => ({text: x.split('|')[1], value: x.split('|')[0]}))
        }
    }
</script>

<style lang="scss">

</style>



