<template>
  <div v-show="showFooter">
    <footer>
       &copy; {{ year }} {{ message }} <a v-for="link in links" :href="link.url">{{ link.label }} </a>
    </footer>

    <og-auth></og-auth>
  </div>
</template>

<script>
  export default {
    name: 'og-footer',
    data () {
      return {
        year: new Date().getFullYear(),
        message: "All rights reserved. OrangeGrid, Inc.",
        links: [
          { label: "Privacy Policy", url: "http://www.orangegrid.com/privacy-policy/" },
          { label: "Terms of Use", url: "http://www.orangegrid.com/terms-of-use" }
        ]
      }
    },
    computed: {
      showFooter(){
        return this.$route.query.footer === 'false' ? false : true;
      }
    }
  }
</script>

<style lang="scss">
  footer {
    background: black;
    bottom: 0;
    color: white;
    font-size: 12px;
    margin: 0 auto;
    padding: 10px;
    position: absolute;
    text-align: center;
    width: 100%;
  }
</style>



