<template>
  <div v-show="showHeader">
    <md-toolbar class="md-dense">
      <md-button class="md-icon-button" @click.native="toggleSidenav">
          <md-icon>menu</md-icon>
      </md-button>

      <div style="flex: 1; display:inline;">
          <a href="/" class="md-title">
              <img src="/content/images/logo/OG_Logo_trimed.png" />
          </a>
          <span><strong><slot name="orgName"></slot></strong></span>
      </div>

      <md-button class="md-accent md-raised" @click.native="clearCache">
          <md-icon>cached</md-icon>
          Clear Cache
      </md-button>
      <md-menu md-align-trigger>
        <md-button md-menu-trigger>
          <md-avatar><slot name="profileImage"></slot></md-avatar>
          <slot name="profileUser"></slot>
          <md-icon>keyboard_arrow_down</md-icon>
        </md-button>
      
        <md-menu-content>
          <slot></slot>
        </md-menu-content>
      </md-menu>
    </md-toolbar>

    <md-sidenav class="md-left" ref="sidenav">
      <md-list class="md-dense" @click.native="toggleSidenav">
        <slot name="sideNav"></slot>
      </md-list>
    </md-sidenav>
  </div>
</template>

<script>
  import notifications from '../services/notifications'
  import og from '../services/og'

  export default {
    name: 'og-header',
    props: [],
    computed: {
      showHeader(){
        return this.$route.query.header === 'false' ? false : true;
      }
    },
    methods: {
      toggleSidenav () {
        this.$refs.sidenav.toggle();
      },
      clearCache () {
        og.theme.clearCache().then(res => {
          notifications.success(res.data.message);
        })
        .catch(e => {
          notifications.error(e.message);
        })
      }
    }
  }
</script>

<style lang="scss">
  body #app {
    &.md-theme-default a:not(.md-button) {
      color: #ff5722;

      &:hover, &:visited {
        color: #ff5722;
      }
    }
  }
</style>


