import Vue from 'vue'
import { storiesOf } from '@storybook/vue'

storiesOf('Textbox', module)
  .add('default',
    () => ({
    template: '<og-text name="field_name" label="Default Field" v-model="fieldValue"></og-text>',
    data: () => ({
      fieldValue: ''
    }),
  }))
  .add('with value',
    () => ({
    template: '<og-text name="field_name" label="With Values" v-model="fieldValue"></og-text>',
    data: () => ({
      fieldValue: 'Lorem Ipsum'
    }),
  }))
  .add('disabled',
    () => ({
    template: '<og-text name="field_name" label="Disabled" v-model="fieldValue" :enableEditMode="false"></og-text>',
    data: () => ({
      fieldValue: 'Lorem Ipsum'
    }),
  }))


