<template>
    <og-link v-if="backLink" :href="backLink" icon="arrow_back" class="link-back">{{label}}</og-link>
</template>

<script>
    import utilities from '../utilities'

    export default {
        name: "og-back-link",
        props: {
            label: {
                type: String,
                default: 'Back'
            },
            parentName: {
                type: String,
                default: undefined
            },
        },
        computed: {
            backLink () {
                if (!this.parentName) {
                    console.warn('OG ALERT: OgBackLink added but parent component name is not defined.')
                    return undefined
                }

                var component = utilities.getParentPath(this, this.parentName)

                if (!component) return undefined

                return component.path
            }
        }
    }
</script>

<style lang="scss">
.link-back {
    color: #ff5722;
    display: inline-block;
    margin-bottom: 20px;

    &:hover {
        color: #f4511e;
    }
}
</style>



