<template>
    <md-avatar class="md-avatar-icon md-accent">
        <img v-if="row.ImageAttachmentID" :src="imgSrc">
        <md-icon v-if="!row.ImageAttachmentID">person</md-icon>
    </md-avatar>
</template>

<script>
    export default {
        props: ['row', 'options'],
        computed: {
            imgSrc () {
                return '/Attachment/Attachment.ashx/View?ID=' + this.row.ImageAttachmentID
            }
        }
    }
</script>


