import Vue from 'vue'
import { storiesOf } from '@storybook/vue'

storiesOf('Model', module)
  .add('default',
    () => ({
    template: '<div><og-button @click="open">Open Modal</og-button><og-modal name="fieldName" :show="show"><og-section>Content here</og-section></og-modal></div>',
    data: () => ({
      show: false
    }),
    methods: {
      open() {
        this.show = true
      }
    }
  }))
  .add('auto open',
    () => ({
    template: '<og-modal name="fieldName" :show="showAutoOpen"><og-progress></og-progress></og-modal>',
    data: () => ({
      showAutoOpen: false
    }),
    created () {
      var self = this
      this.$nextTick(() => {
        self.open()
      })
    },
    methods: {
      open() {
        this.showAutoOpen = true
      }
    }
  }))
  .add('title and sub',
    () => ({
    template: '<div><og-button @click="open">Open Modal</og-button><og-modal name="fieldName" title="title here" subhead="sub title here" :show="show"><og-progress></og-progress></og-modal></div>',
    data: () => ({
      show: false
    }),
    methods: {
      open() {
        this.show = true
      }
    }
  }))
  .add('disable close icon',
    () => ({
    template: '<div><og-button @click="open">Open Modal</og-button><og-modal name="fieldName" :showCloseIcon="false" :show="show"><og-section>Content here</og-section></og-modal></div>',
    data: () => ({
      show: false
    }),
    methods: {
      open() {
        this.show = true
      }
    }
  }))


