<template>
    <og-block name="filterBlock1" layout="100">
        <div v-for="(row_column, columnIndex) in columns" :key="columnIndex">
            <component 
                v-if="row_column.filterable"
                v-model="row_column.filter_text"
                :is="columnType(row_column)"
                :label="row_column.filterLabel || row_column.label"
                :name="row_column.label"
                :options="row_column.datasource"
                @input="updateColumn($event, columnIndex)">
            </component>
        </div>
        <og-block layout="70 15 15">
            <span></span>
            <og-button icon="clear_all" @click="clear">Clear Fields</og-button>
            <og-button icon="filter_list" @click="close">Filter</og-button>
        </og-block>
    </og-block>
</template>

<script>
    export default {
        name: 'og-table-filter-server-modal',
        props: {
            columns: {
                type: Array,
                default: undefined
            }
        },
        methods: {
            clear () {
                var self = this

                var i = 0
                this.columns.forEach(col => {
                    col.filter_text = ''
                    self.$set(self.columns, i, col)
                    i++
                })
                this.$nextTick(() => {
                    this.$emit('update')
                })
            },
            close () {
                this.$emit('close')
            },
            columnType (row) {
                if (row.filterType == Boolean) {
                    return 'og-check'
                } else if (row.filterType == 'dropdown') {
                    return 'og-dropdown'
                }

                return 'og-text'
            },
            updateColumn (val, index) {
                console.log('update the value')
            }
        }
    }
</script>

<style lang="scss">

</style>


