<template>
  <og-modal name="authmodal" :show="show" size="large" title="Your session is about to EXPIRE in:" :showCloseIcon="false" class="auth-modal">
    
    <og-block layout="100 100" class="counter">
      <span>
        <span class="counter-seconds">{{ counter }}</span> Seconds
      </span>
    </og-block>

    <og-block slot="footer" layout="35 30 35">
      <span></span>
      <og-button buttonClass="md-primary" icon="" @click="keepAlive">Continue your session?</og-button>
      <span></span>
    </og-block>
  </og-modal>
</template>

<script>
  import og from '../services/og'
  import utilities from '../utilities'

  const redirectUrlAuth = '/Auth/Login?Timeout=1&ReturnUrl='
  const redirectUrlSlo = '/Auth/SamlTimeout/'

  export default {
    name: 'og-auth',
    data () {
      return {
        authenticated: undefined,
        counter: undefined,
        counterInterval: undefined,
        isSaml: undefined,
        secondsRemaining: undefined,
        show: false
      }
    },
    created () {
      this.checkAuth()
    },
    methods: {
      checkAuth () {
        var self = this

        og.auth.TicketDetails().then(response => {
          self.authenticated = response.data.Authenticated
          self.isSaml = response.data.IsSaml
          self.secondsRemaining = response.data.SecondsRemaining

          if (!self.authenticated) {
            self.counter = undefined
            self.show = false

            var sloCookie = utilities.getCookie('og.Slo')
            if (sloCookie) {
              window.location = redirectUrlSlo + sloCookie
            } else {
              window.location = redirectUrlAuth + encodeURIComponent(window.location.pathname + window.location.search)
            }
          } else {
            if (self.secondsRemaining <= 65) {
              self.show = true
              self.counter = self.secondsRemaining

              self.counterInterval = setInterval(() => {
                self.counter--
                if (self.counter <= 0) {
                  clearInterval(self.counterInterval)
                  self.checkAuth()
                }
              }, 1000)
            } else {
              self.counter = undefined
              self.show = false

              var timeoutSeconds = self.secondsRemaining - 60
              timeoutSeconds = Math.max(timeoutSeconds, 0)
              if (timeoutSeconds > 600) timeoutSeconds = 600

              setTimeout(self.checkAuth, timeoutSeconds * 1000)
            }
          }
          
        }).catch(response => {
          window.location = redirectUrlAuth + encodeURIComponent(window.location.pathname)
        })
      },
      keepAlive () {
        var self = this
        og.auth.KeepAlive().then(response => {
          self.counter = undefined
          self.show = false
          setTimeout(self.checkAuth, 60000)
        })
      },
    }
  }
</script>

<style lang="scss">
  .auth-modal {
    .md-dialog {
      border: 5px solid orangered;
      
      .md-dialog-title {
        background-color: orangered;
        color: white;
        padding: 12px 24px 12px 0;
        text-align: center;
      }

      .md-dialog-content {
        font-size: 24px;
        .counter .md-layout {
          justify-content: center;

          .counter-seconds {
            color: orangered;
            font-size: 28px;
            font-weight: bold;
          }
        }
      }

      .md-dialog-actions {
        .md-layout {
          justify-content: center;
        }
      }
    }
  }
</style>



