<template>
	<md-toolbar>
		<og-block layout="60 40">
			<h1 class="md-title">{{title}}</h1>
			<og-block layout="50 50" id="table_action_buttons">
				<og-text v-if="filterable && !dynamicFilter" id="filter" label="Filter" v-model="filter_text" @change.stop=""></og-text>
				<og-button v-if="filterable && dynamicFilter" :buttonRaised="false" @click.native="clickFilterAction">
					<og-icon icon="filter_list"></og-icon> Filter
				</og-button>
				<og-block :layout="tableActionLayout">
					<og-button
						v-for="(tableAction, key, index) in table_actions"
						:icon="tableAction.icon"
						@click="clickTableAction($event, tableAction.action)"
						:key="key" :hideLabel="hideLabel">
							{{tableAction.label}}
					</og-button>
				</og-block>
			</og-block>
		</og-block>
	</md-toolbar>
</template>

<script>
import utilities from '../../../utilities'
const splitLimit = 30
const labelLimit = 45

export default {
	props: {
		tableActions: [Array],
		filterable: {
			type: Boolean, 
			default: false
		},
		dynamicFilter: {
			type: Boolean,
			default: false
		},
		title: {
			type: String,
			default: ''
		}
	},
	data: function(){return {
		filter_text: ''
	}},
	computed: {
		table_actions (){
			return utilities.mapPropertyArray(this.tableActions, s => ({action: s, icon: s}))
		},
		tableActionSplit () {
			// Get the best split for the number of table actions
			var splitRounded = Math.round(100 / this.tableActions.length)

			// round to the nearest 5 due to css selectors
			splitRounded = Math.ceil(splitRounded / 5) * 5
			
			// lets check to see if we need to 
			if (splitRounded < splitLimit) {
				splitRounded = splitLimit
			}

			return splitRounded;
		},
		tableActionLayout () {
			// assign numbers to array equal to the number of objects
			var layout = []
			if(this.tableActions){
				this.tableActions.forEach((x) => layout.push(this.tableActionSplit));
			}

			// double check that we equal 100
			return layout.join(' ')
		},
		hideLabel () {
			return this.tableActionSplit < labelLimit
		}
	},
	methods: {
		clickTableAction: function(event, action) {
			this.$emit('table-action', action);
		},
		clickFilterAction: function(event) {
			this.$emit('filterClick')
		}
	},
	watch: {
		filter_text: function(){
			this.$emit('change', this.filter_text);
		},
	}
}
</script>

<style lang="scss">
	.md-toolbar .md-layout {
		.md-title {
			margin-top: 20px;
		}

		.md-input-container.og-text {
			min-height: 48px;
		}

		.md-input-container input,
		.md-input-container label,
		.md-input-container textarea,
		.md-input-container.md-input-focued input,
		.md-input-container.md-input-focued textarea {
			color: rgba(0,0,0,.87) !important;
			text-shadow: 0 0 0 rgba(0, 0, 0, .87) !important;		
		}

		#table_action_buttons {
			text-align: right;

			.md-button {
				margin-top: 16px;
				max-height: 36px;
			}
		}
	}
</style>


