<template>
    <md-icon>{{icon}}</md-icon>
</template>

<script>
    /******
    
    To find new and exciting icons please have a look at:
    https://material.io/icons/

    Also note that if there is a space in the icon name the space
    shall be replaced with an underscore (i.e. "account circle"
    should become "account_circle").

    ******/

    export default {
        props: ['icon']
    }
</script>

<style scoped>
    .md-theme-default.md-input-container i.md-icon:after, 
    .md-theme-og.md-input-container i.md-icon:after {
        background: transparent;
    }
</style>


