import X2JS from 'x2js'

export default {
    data () {
        return {
            editable: undefined,
            inputValue: undefined,
            label: undefined,
            name: undefined,
            pattern: undefined,
            title: undefined,
            validation: {
                type: String,
                default: undefined
            }
        }
    },
    props: {
        fieldDefinition: { type: Object, default: {} },
        value: { type: String, default: undefined },
    },
    created () {
        this.applyDefaultFields()
    },
    methods: {
        addValidation (validator) {
            if (this.validation) {
                this.validation = this.validation + "|" + validator
            } else {
                this.validation = validator
            }
        },
        applyDefaultFields () {
            this.inputValue = this.value
            this.label = this.fieldDefinition.DisplayName || this.fieldDefinition.Name
            
            if (this.fieldDefinition.isAddTableName) {
                this.name = this.fieldDefinition.TableName + '_' + this.fieldDefinition.FieldDefinition
            } else {
                this.name = this.fieldDefinition.FieldDefinitionID
            }

            if (this.fieldDefinition.IsRequired || this.getRequiredAttribute()) {
                this.addValidation('required')
            }
            
            this.editable = !(this.fieldDefinition.IsEditable || '1').bool()


            // caching
            // var key = cacheObject[fieldDefinitionItem.TableBindOverride || fieldDefinitionItem.TableName];
            // if (!model.isCacheObjectEmpty && (key)) {
            //     model.value = key[fieldDefinitionItem.FieldDefinition];
            // }
            
        },
        getRequiredAttribute () {
            var requiredDependencies = this.fieldDefinition.RequiredDependencies
            if (!requiredDependencies) return false

            var xmlParser = new X2JS();
            var depXml = xmlParser.xml2js(requiredDependencies)

            if (!depXml || !depXml.XmlDependencies || !depXml.XmlDependencies.Dependency) return false;

            var dep = depXml.XmlDependencies.Dependency
            for (var i = 0; i < dep.length; i++) {
                var isRequired = false
                var dependency = dep[i],
                    altTableName = dependency['AltTableName'],
                    fieldDefinition = dependency['FieldDefinition'],
                    fieldDefinitionID = dependency['FieldDefinitionID'],
                    operation = dependency['Operation'],
                    value = dependency['Value']
                
                // cache look up

                switch (operation) {
                    case "==":
                        console.log("cache === value")
                        isRequired = false
                        break
                    case ">":
                        console.log("cache === value")
                        isRequired = false
                        break
                    case "<":
                        console.log("cache === value")
                        isRequired = false
                        break
                    default:
                        break
                }

                console.log(dependency, altTableName, fieldDefinition, fieldDefinitionID, operation, value)

                if (isRequired) return true
            }
            
            return false;
        },
        update (val) {
            this.$forceUpdate()
            this.$emit('input', val)
        }
    }
}


