<template>
    <og-text 
        :name="name"
        :label="label"
        v-model="maskedInputValue"
        :icon="icon"
        :inputType="inputType"
        :pattern="pattern"
        :title="title"
        :readonly="mask"
        :buttonEnabled="true"
        :buttonIcon="buttonIcon"
        @buttonClicked="toggle4Digit"
        @input="update"></og-text>
</template>

<script>
    import mixin from './mixin.js'

    export default {
        name: "og-field-last4digit",
        mixins: [ mixin ],
        data () {
            return {
                buttonIcon: 'visibility_off',
                icon: undefined,
                inputType: "text",
                mask: true,
            }
        },
        computed: {
            maskedInputValue () {
                if (this.mask) {
                    if (!this.inputValue) return "";
                    
                    var input = JSON.parse(JSON.stringify(this.inputValue))
                    if (input.length > 4)
                        return "*" + input.substring(input.length - 4, input.length)
                    else
                        return "*" + input
                } else {
                    return this.inputValue
                }
            }
        },
        methods: {
            toggle4Digit () {
                // set mask
                this.mask = !this.mask
                
                // change icon
                this.buttonIcon = this.buttonIcon === 'visibility' ? this.buttonIcon = 'visibility_off' : this.buttonIcon = 'visibility'
            },
            update (val) {
                // force vue to update
                this.$forceUpdate()
                // replace local modal
                this.inputValue = val
                // emit event for parent model change
                this.$emit('input', val)
            }
        }
    }
</script>

<style lang="scss">

</style>



