<script>
/******* 
  This component implements a render function therefore a template
  is prohibited from being added to this vue file.
*******/

export default {
  name: 'og-block',
  props: ['layout', 'name'],
  methods: {
    parseLayout(layout){
      // 50 50 | 25 25 25 25
      var tempLayout = layout.split('|');//.filter(function(el) {return el.length != 0});
      for (var i = 0; i < tempLayout.length; i++) {
        tempLayout[i] = tempLayout[i].split(' ').filter(function(el) {return el.length != 0}).map(x => parseInt(x));
      }
      return tempLayout;
    },
    getChildren() {
      var children = this.$slots.default ? this.$slots.default : this.$slots;
      return children && children.length ? children.filter(c => c.tag) : [];
    },
    createRoot(createElement, content) {
      return createElement('md-layout', {
        attrs: {
          'md-column': true
        }
      }, content);
    },
    createRow(createElement, cols) {
      return createElement('md-layout', {
        attrs: {
          'md-row': true, 'md-gutter': true
        }
      }, cols);
    },
    createColumn(createElement, colLayout, numberOfColumns, content){
      return createElement('md-layout', {
          attrs: {
            'md-flex-xsmall':'100', 'md-flex-small':numberOfColumns % 2 == 0 ? "50" : "100", 'md-flex-medium': colLayout, 'md-flex-large': colLayout, 'md-flex-xlarge': colLayout
          }
        }, [content]);
    }
  },
  render(createElement){
    // we use the render function here to avoid having to name slots
    // when specifying child content for usages of this component
    var rows = [];
    var children = this.getChildren();
    var slot = 0;

    if(this.layout) {
      // render content that fits into the provided layout
      var rowLayouts = this.parseLayout(this.layout);
      
      for (var ri = 0; ri < rowLayouts.length; ri++) {
        var rowLayout = rowLayouts[ri];
        var cols = [];
        for (var ci = 0; ci < rowLayout.length; ci++) {
          var colLayout = rowLayout[ci];
          cols.push(this.createColumn(createElement, colLayout, rowLayout.length, children[slot]));
          slot++;
        }

        rows.push(this.createRow(createElement, cols));
      }
    }

    // render additional content in a "100" row
    for (var slot = slot; slot < children.length; slot++) {
      var column = this.createColumn(createElement, "100", 1, children[slot]);
      rows.push(this.createRow(createElement, [column]));
    }

    return this.createRoot(createElement, rows);
  }
//});
}
</script>

<style lang="scss">
  .md-layout {
    @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
    /* IE10+ CSS styles go here */
      -ms-flex: 1 1 auto;
      flex: 1 1 auto;
    }

    div:not(.md-layout) {
      flex:1 0 100%;
    }
  }

  .md-layout button.md-button.md-raised {
    margin-bottom: 15px;
  }
</style>


