<template>
  <md-card>
      <md-toolbar class="md-dense">
        <h2>{{title}}</h2>
      </md-toolbar>

    <md-card-content>
      <slot>Your slot seems lonely. Put something in it!</slot>
    </md-card-content>
  </md-card>
</template>

<script>
export default {
  name: 'og-panel',
  props: ['title']
}
</script>

<style lang="scss" scoped>
.md-toolbar {
  border-bottom: 1px solid #bbb;
}
</style>


