<template>
    <div v-if="!fileTemplate">
        <md-progress class="progress md-accent" md-indeterminate></md-progress>
        <div>Please select a file template from the prior step.</div>
    </div>
    <og-section :title="getTitle" v-else>
        <og-block layout="100">
            <div v-if="fileTemplate.Instructions">{{ fileTemplate.Instructions }}</div>
            <div v-if="useModal && modalFields">
                <div v-for="(row, index) in modalFields" :key="index">
                    <og-block :layout="getLayout(row.FieldDefinitions)">
                        <component
                            v-for="(fieldDef, fdIndex) in row.FieldDefinitions"
                            :key="fdIndex"
                            :is="componentType(fieldDef)"
                            v-model="model[fieldDef.FieldDefinition]"
                            @input="updateModel"
                            :fieldDefinition="fieldDef">
                        </component>
                    </og-block>
                </div>
            </div>

            <div v-if="useHeader && headerFields">
                <div v-for="(row, index) in headerFields" :key="index">
                    <og-block :layout="getLayout(row.FieldDefinitions)">
                        <component
                            v-for="(fieldDef, fdIndex) in row.FieldDefinitions"
                            :key="fdIndex"
                            :is="componentType(fieldDef)"
                            v-model="model[fieldDef.FieldDefinition]"
                            @input="updateModel"
                            :fieldDefinition="fieldDef">
                        </component>
                    </og-block>
                </div>
            </div>

            <div v-if="useOther && otherFields">
                <div v-for="(row, index) in otherFields" :key="index">
                    <og-block :layout="getLayout(row.FieldDefinitions)">
                        <component
                            v-for="(fieldDef, fdIndex) in row.FieldDefinitions"
                            :key="fdIndex"
                            :is="componentType(fieldDef)"
                            v-model="model[fieldDef.FieldDefinition]"
                            @input="updateModel"
                            :fieldDefinition="fieldDef">
                        </component>
                    </og-block>
                </div>
            </div>

            <div v-if="useHidden && hiddenFields">
                <div v-for="(row, index) in hiddenFields" :key="index">
                    <og-block :layout="getLayout(row.FieldDefinitions)">
                        <component
                            v-for="(fieldDef, fdIndex) in row.FieldDefinitions"
                            :key="fdIndex"
                            :is="componentType(fieldDef)"
                            v-model="model[fieldDef.FieldDefinition]"
                            @input="updateModel"
                            :fieldDefinition="fieldDef">
                        </component>
                    </og-block>
                </div>
            </div>
            <og-dropdown v-if="useAddressBook" :options="addressBookList" v-model="model.defaultAddressBookCollectionID" @input="updateModel" label="Default Address Book"></og-dropdown>
        </og-block>
    </og-section>
</template>

<script>
    import http from '../../services/http'
    import og from '../../services/og'

    import OgFieldCheckbox from './og-field-checkbox.vue'
    import OgFieldControlDefinitionAttachment from './og-field-controldefinition-attachment.vue'
    import OgFieldControlDefinitionLookup from './og-field-controldefinition-lookup.vue'
    import OgFieldDatePicker from './og-field-datepicker.vue'
    import OgFieldDateTimePicker from './og-field-datetimepicker.vue'
    import OgFieldDropdown from './og-field-dropdown.vue'
    import OgFieldLast4Digit from './og-field-last4digit.vue'
    import OgFieldRadio from './og-field-radio.vue'
    import OgFieldTextarea from './og-field-textarea.vue'
    import OgFieldTextbox from './og-field-textbox.vue'

    export default {
        name: "og-field-definition",
        props: [
            'model',
            'pageLayoutID',
            'parentObject',
            'templateID',
            'templateType',
            'useAddressBook',
            'useHeader',
            'useHidden',
            'useModal',
            'useOther'
        ],
        data () {
            return {
                addressBookList: [],
                fileTemplate: undefined,
                templateFileDefinitions: [],
            }
        },
        watch: {
            templateID () {
                this.model = {}
                this.getTemplates()
            },
        },
        computed: {
            getTitle() {
                return 'New ' + this.fileTemplate.FileTemplate
            },
            hiddenFields() {
                if (!this.templateFileDefinitions) return undefined
                var data = this.hiddenFieldData()
                if (!data || data.length == 0) return undefined
                
                return this.processFields(data)
            },
            headerFields() {
                if (!this.templateFileDefinitions) return undefined
                var data = this.headerFieldData()
                if (!data || data.length == 0) return undefined
                
                return this.processFields(data)
            },
            modalFields() {
                if (!this.templateFileDefinitions) return undefined
                var data = this.modalFieldData()
                if (!data || data.length == 0) return undefined
                
                return this.processFields(data)
            },
            otherFields() {
                if (!this.templateFileDefinitions) return []
                var data = this.otherFieldData()
                if (!data || data.length == 0) return undefined
                
                return this.processFields(data)
            }
        },
        methods: {
            componentType (fieldDef) {
                var controlType = (fieldDef.ControlType || 'default').toLowerCase()
                
                // link ??
                
                if ('checkbox' === controlType) return 'og-field-checkbox'
                if ('datepicker' === controlType) return 'og-field-datepicker'
                if ('datetimepicker' === controlType) return 'og-field-datetimepicker'
                if ('dropdown' === controlType || 'select' === controlType) return 'og-field-dropdown'
                if ('textarea' === controlType) return 'og-field-textarea'
                if ('radio' === controlType) return 'og-field-radio'
                if ('last4digit' === controlType) return 'og-field-last4digit'
                if ('controldefinition' === controlType) {
                    var controlDefinitionType = fieldDef.ControlDefinition.ControlDefinitionType || fieldDef.ControlDefinitionType;

                    if ('Attachment' === controlDefinitionType) return 'og-field-controldefinition-attachment'
                    if ('Lookup' === controlDefinitionType) return 'og-field-controldefinition-lookup'
                    throw new Error('The control does not exist.')
                }
                
                return 'og-field-textbox'
            },
            getLayout (array){
                if (!array || array.length === 0) return ''

                return array.map(x => "50").join(' ')
            },
            getTemplates () {
                if (!this.templateID) {
                    this.fileTemplate = undefined
                    this.templateFileDefinitions = undefined
                    return
                } 
                if (this.templateID) {
                    var self = this

                    var fileTemplateQuery = {
                        FileTemplateID: this.templateID,
                        PageLayoutID: this.pageLayoutID
                    }

                    og.application.fileTemplate.search(fileTemplateQuery)
                        .then(res => {
                            self.fileTemplate = res.data.FileTemplateCollection.FileTemplateItem[0]

                            var trackFileDefinitionQuery = {
                                TemplateID: self.templateID,
                                TemplateType: self.templateType,
                                PageLayoutID: self.pageLayoutID
                            }
                            
                            og.contact.collection._parseFieldDefinitionDetailsToLayoutJSON(trackFileDefinitionQuery)
                                .then(res => self.templateFileDefinitions = res)

                            if (self.fileTemplate.AddressBookCollectionID) {

                            }
                        })
                    
                    http.get('/Contact/GetUserAddressBookLookup')
                        .then(res => res.data.map(m => ({ text: m.Collection, value: m.CollectionID })))
                        .then(res => { self.addressBookList = res })
                }
            },
            hiddenFieldData() {
                return this.templateFileDefinitions.hidden
            },
            headerFieldData() {
                return this.templateFileDefinitions.header
            },
            modalFieldData() {
                return this.templateFileDefinitions.modal
            },
            otherFieldData() {
                return this.templateFileDefinitions.other
            },
            processFields(data) {
                var self = this
                
                var rows = data[0].Tabs[0].Subtabs[0].sections[0].rows
                rows.forEach(row => {
                    row.FieldDefinitions.forEach(fd => {
                        if (self.parentObject && self.parentObject.hasOwnProperty(fd.FieldDefinition)) {
                            // Check if field exists in parent model.
                            self.model[fd.FieldDefinition] = self.parentObject[fd.FieldDefinition]
                        } else if (!self.model.hasOwnProperty(fd.FieldDefinition)) {
                            // check if the value is not defined and set value
                            self.model[fd.FieldDefinition] = fd.SelectedValue
                        }
                    })
                })

                // ensure model is updated after update.
                this.updateModel()

                return data[0].Tabs[0].Subtabs[0].sections[0].rows
            },
            updateModel () {
                this.$forceUpdate()
                this.$emit('update')
            },
        },
        components: {
            'og-field-checkbox': OgFieldCheckbox,
            'og-field-controldefinition-attachment': OgFieldControlDefinitionAttachment,
            'og-field-controldefinition-lookup': OgFieldControlDefinitionLookup,
            'og-field-datepicker': OgFieldDatePicker,
            'og-field-datetimepicker': OgFieldDateTimePicker,
            'og-field-dropdown': OgFieldDropdown,
            'og-field-last4digit': OgFieldLast4Digit,
            'og-field-radio': OgFieldRadio,
            'og-field-textarea': OgFieldTextarea,
            'og-field-textbox': OgFieldTextbox,
        }
    }
</script>

<style lang="scss" scoped>
    .progress {
        left: 0;
        position: absolute;
        top: 0;
    }
</style>



