<template>
  <md-whiteframe style="padding: 20px; border-left: solid 3px gray">
    <og-link v-if="removable" class="remove-operation-button" icon="close" @click="removeOperation($event)"></og-link>
    <h2>Batch</h2>
    <h3 v-if="!editableTitle">{{title}}</h3>
    <og-text v-else v-model="title" :label="titleLabel"></og-text>
    <og-block layout="33 33 33">
      <og-text label="Module" v-model="operation.module"></og-text>
      <og-text label="Operation" v-model="operation.operation"></og-text>
      <div>
        <md-switch v-model="operation.async">Async</md-switch>
      </div>
    </og-block>
    <og-operation :operation="operation.queryOperation" title="Query Operation" @remove="clearQueryOperation" removeable></og-operation>
    <og-operation :operation="operation.actionOperation" newOperationTypeLabel="Action Operation Type" title="Action Operation" new-operation-text="Create Action Operation" @remove="removeActionOperation" @newOperationType="newActionOperationType" removeable></og-operation>
    <!--<md-chips v-if="operation.parent" v-model="operation.tokens" md-input-placeholder="Output Tokens" style="margin-left: 7px; margin-right: 26px;">
      <template scope="chip">{{ chip.value }}</template>
    </md-chips>-->
  </md-whiteframe>
</template>

<script>
  import operation from './operation'
  import utilities from '../../utilities'

  export default {
    name: "og-batch-operation",
    mixins: [operation],
    props: {
      removable: Boolean
    },
    methods: {
      clearQueryOperation(){
        this.operation.queryOperation = this.createSimpleOperationModel();
      },
      removeActionOperation(){
        this.operation.actionOperation = this.createEmptyOperationModel();
      },
      newActionOperationType($event, operationType) {
        var actionOperation;

        if(operationType === 'BatchOperation'){
          actionOperation = this.createBatchOperationModel();
        } else if(operationType === 'CompoundOperation'){
          actionOperation = this.createCompoundOperationModel();
        } else if(operationType === 'Operation'){
          actionOperation = this.createSimpleOperationModel();
        }

        this.operation.actionOperation = actionOperation;
      }
    }
  }
</script>

<style lang="scss" scoped>
  .remove-operation-button {
    float: right;
  }
</style>


