import utilities from '../../utilities'

export default {
    props: {
        operation: Object,
        title: String,
        titleLabel: String,
        editableTitle: Boolean,
        removable: Boolean
    },
    methods: {
        removeOperation($event){
            this.$emit('remove', this.operation);
        },
        createBaseOperationModel(){
            return {
                key: utilities.guid(),
                module: undefined,
                operation: undefined
            }
        },
        createEmptyOperationModel(){
            var model = this.createBaseOperationModel();
            model.type = "Empty";
            return model;
        },
        createSimpleOperationModel(){
            var model = this.createBaseOperationModel();
            model.type = "Operation";
            model.params = [];
            return model;
        },
        createBatchOperationModel(){
            var model = this.createBaseOperationModel();
            model.operation = "BatchOperation";
            model.type = "BatchOperation";
            model.async = false;
            model.queryOperation = this.createSimpleOperationModel();
            model.actionOperation = this.createEmptyOperationModel();
            return model;
        },
        createCompoundOperationModel(){
            var model = this.createBaseOperationModel();
            model.operation = "CompoundOperation";
            model.type = "CompoundOperation";
            model.children = [];
            return model;
        },
        newOperationType($event, operationType) {
            var operation;

            if(operationType === 'BatchOperation'){
                operation = this.createBatchOperationModel();
            } else if(operationType === 'CompoundOperation'){
                operation = this.createCompoundOperationModel();
            } else if(operationType === 'Operation'){
                operation = this.createSimpleOperationModel();
            }

            this.operation = operation;
        }
    }
}


