<template>
  <md-input-container v-show="showInput">
    <label :for="name">{{label}}</label>
    <md-textarea class="og-edit-code" :name="name" v-model="inputValue" @input="input($event)" :disabled="!enableInput"></md-textarea>
    <span v-show="error" class="md-error"><og-icon v-show="error" icon="error" class="md-accent"></og-icon> {{ error }}</span>
  </md-input-container>
</template>

<script>
  import input from '../mixins/input'
  import permissionsEdit from '../mixins/permission-edit'
  import permissionsView from '../mixins/permission-view'

  export default {
    name: 'og-code-editor',
    mixins: [input, permissionsEdit, permissionsView],
    created () {
      this.inputValue = this.value
    }
  }
</script>

<style lang="scss" scoped>
  .og-edit-code {
    background: rgba(244,81,30,.07);
  }
</style>



