<template>
    <md-button :name="name" :class="'md-theme-og ' +  buttonClass + (buttonRaised ? ' md-raised ': '')" @click.native="click" :disabled="disabled">
      <og-icon v-if="icon" :icon="icon"></og-icon>
      <span v-if="!hideLabel"><slot>Submit</slot></span>
    </md-button>
</template>

<script>
export default {
  name: 'og-button',
  props: {
    name: {
      type: String
    },
    disabled: {
      type: Boolean,
      default: false
    },
    icon: {
      type: String
    },
    hideLabel: {
      type: Boolean,
      default: false
    },
    buttonClass: {
      type: String,
      default: 'md-accent'
    },
    buttonRaised: {
      type: Boolean,
      default: true
    }
  },
  methods: {
  	click($event){
  		this.$emit("click", $event);
  	}
  }
}
</script>

<style lang="scss" scoped>
.md-button {
  flex: 1;

  &.btn-max-height-36 {
    max-height: 36px;
  }
}
</style>


