import Vue from 'vue'
import { storiesOf } from '@storybook/vue'

storiesOf('Block', module)
  .add('default',
    () => ({
    template: '<og-block layout="100 | 50 50 | 100"><div>Og-block is used to layout the content. An attribute of layout is used to define how a piece of content is to be diveded with the block. A block is any html element (i.e. div, span, a, or another other og-{type}). Defining it at "50 50" will make the blocks split evenly with a gutter in between the two blocks. To add new rows the layout code is separated with a | (pipe). An example of two rows with the first one being 50 50 and the second taking up 100% would be: "50 50 | 100". The acceptable values for a layout are increments of 5 as well as 33 and 66.</div><div style="background-color: red;">test</div><div style="background-color: green;">another block</div><div style="background-color: orange;">Full width</div></og-block>',
  }))


