<template>
  <og-button @click="options.click(row)" :icon="options.icon">{{ options.label }}</og-button>
</template>

<script>
  export default {
    props: ['row', 'options'],
    computed: {
      label () {
        if (typeof this.options.label == 'function') {
          return this.options.label(this.row)
        }

        return this.options.label
      }
    }
  }
</script>


