<template>
    <og-table :columns="columns" :rows="rows" :row-actions="rowActions" :table-actions="tableActions" paginate filterable sortable @row-action="rowAction" @table-action="tableAction"></og-table>
</template>

<script>
    export default {
        data() {
            return {
                tableActions: [],
                rowActions: [{
                    action: 'select',
                    icon: 'add'
                }],
                columns: [{
                    label: 'User',
                    field: 'Person'
                },
                {
                    label: 'First Name',
                    field: 'FirstName'
                },
                {
                    label: 'Last Name',
                    field: 'LastName'
                },
                {
                    label: 'Middle Name',
                    field: 'MiddleName'
                },
                {
                    label: 'Created By',
                    field: 'CreatedPerson'
                },
                {
                    label: 'Created Date',
                    field: 'CreatedDate'
                },
                {
                    label: 'Updated By',
                    field: 'UpdatedPerson'
                },
                {
                    label: 'Updated Date',
                    field: 'UpdatedDate'
                }]
            };
        },
        props: ['rows'],
        methods: {
            rowAction(link, action){
                this.$emit(action, link);
            },
            tableAction(action){
                this.$emit(action);
            }
        }
    }

</script>

<style lang="scss" scoped>
</style>


