import Vue from 'vue'
import { storiesOf } from '@storybook/vue'

storiesOf('File', module)
  .add('default',
    () => ({
    template: '<og-file label="File Input"></og-file>',
  }))
  .add('with model binding',
    () => ({
    template: '<og-file label="Model Bound" name="fieldNameID" v-model="model"></og-file>',
    data: () => ({
      model: undefined
    })
  }))
  .add('with image upload',
    () => ({
    template: '<og-file label="Image Upload" name="fieldNameID" v-model="model" :accept="accept"></og-file>',
    data: () => ({
      accept: 'image/*',
      model: undefined
    })
  }))
  .add('allow multiple',
    () => ({
    template: '<og-file label="Image Upload" name="fieldNameID" v-model="model" :multiple="multiple"></og-file>',
    data: () => ({
      model: undefined,
      multiple: true
    })
  }))
  .add('selected event',
    () => ({
    template: '<og-file label="Model Bound" name="fieldNameID" v-model="model" @selected="selected($event)"></og-file>',
    data: () => ({
      model: undefined
    }),
    methods: {
      selected(evt) {
        alert(evt[0].name)
      }
    }
  }))


