import Vue from 'vue'
import { storiesOf } from '@storybook/vue'

storiesOf('Code Editor', module)
  .add('default',
    () => ({
    template: '<og-code-editor name="field_name" label="Code Editor Label" v-model="fieldValue"></og-code-editor>',
    data: () => ({
      fieldValue: '<html>\r\n<body>\r\n<strong>Test html body</strong>\r\n</body>\r\n</html>'
    })
  }))
  .add('empty',
    () => ({
    template: '<og-code-editor name="field_name" label="Code Editor Label" v-model="fieldValue"></og-code-editor>',
    data: () => ({
      fieldValue: ''
    })
  }))
  .add('disabled',
    () => ({
    template: '<og-code-editor name="field_name" label="Code Editor Label" v-model="fieldValue" :enableEditMode="false"></og-code-editor>',
    data: () => ({
      fieldValue: '<html>\r\n<body>\r\n<strong>Test html body</strong>\r\n</body>\r\n</html>'
    })
  }))


