<template>
    <og-modal name="userFinder" title="Add Users" :show="show" @close="close">
        <og-user-list :rows="rows" @select="select"></og-user-list>
    </og-modal>
</template>

<script>
import notifications from '../services/notifications'
import utilities from '../utilities'
import og from '../services/og'

export default {
    data(){
        return {
            rows: []
        }
    },
    props: ['show','exclude'],
    created() {
        this.reload();
    },
    watch: {
        exclude(val) {
            if (val && val.length > 0) {
                this.reload()
            }
        }
    },
    methods: {
        reload(){
            var self = this;
            og.security.person.search({DisplaySize: 0}).then(function (response) {
                var data = response.data.PersonCollection.PersonItem;
                
                if (self.exclude && self.exclude.length > 0) {
                    self.exclude.forEach(ex => {
                        var index = data.findIndex(d => {
                            return d.PersonID == ex.PersonID
                        })
                        if (index > -1) {
                            data.splice(index, 1)
                        }
                    })
                }
                self.rows = data;
            });
        },
        select(user){
            this.$emit('select', user);
        },
        close(){
            this.$emit('close');
        }
    }
} 
</script>

<style lang="scss">
</style>


