import Vue from 'vue'
import { storiesOf } from '@storybook/vue'

storiesOf('Editor', module)
  .add('default',
    () => ({
    template: '<og-editor name="editor"><og-text label="Field Name" :value="fieldValue"></og-text></og-editor>',
    data: () => ({
      fieldValue: 'demo'
    })
  }))
  .add('as modal',
    () => ({
    template: '<div><og-button @click="open">Open Modal</og-button><og-editor name="editor" @close="close" :modal="true" :show="show" title="Title Here" subhead="Sub-Title" meta="Meta information here" :editMode="editable"><og-text label="Field Name" :value="fieldValue"></og-text></og-editor></div>',
    data: () => ({
      fieldValue: 'demo',
      show: false
    }),
    methods: {
      open() {
        this.show = true
      },
      close() {
        this.show = false
      }
    }
  }))
  .add('with title',
    () => ({
    template: '<og-editor name="editor" title="Title Here" subhead="Sub-Title"><og-text label="Field Name" :value="fieldValue"></og-text></og-editor>',
    data: () => ({
      fieldValue: 'demo'
    })
  }))
  .add('with meta',
    () => ({
    template: '<og-editor name="editor" title="Title Here" subhead="Sub-Title" meta="Meta information here"><og-text label="Field Name" :value="fieldValue"></og-text></og-editor>',
    data: () => ({
      fieldValue: 'demo'
    })
  }))
  .add('with edit mode',
    () => ({
    template: '<og-editor name="editor" title="Title Here" subhead="Sub-Title" meta="Meta information here" :editMode="editable"><og-text label="Field Name" :value="fieldValue"></og-text></og-editor>',
    data: () => ({
      fieldValue: 'demo',
      editable: true
    })
  }))
  .add('with events',
    () => ({
    template: '<og-editor name="editor" @save="save" @close="close" @reset="reset"><og-text label="Field Name" :value="fieldValue"></og-text></og-editor>',
    data: () => ({
      fieldValue: 'demo'
    }),
    methods: {
      save() {
        console.log('save')
      },
      close() {
        console.log('close')
      },
      reset() {
        console.log('reset')
      }
    }
  }))


