<template>
    <og-check
        :name="name"
        :label="label"
        v-model="inputValue"
        :pattern="pattern"
        :title="title"
        @input="update"></og-check>
</template>

<script>
    import helpers from '../mixins/helpers.js'
    import mixin from './mixin.js'

    export default {
        name: "og-field-checkbox",
        mixins: [ mixin ],
        created () {
            
        }
    }
</script>

<style lang="scss">

</style>



