<template>
<og-block>
  <og-block :name="name" :layout="layout">
    <component v-for="(control, key, index) in controls" :is="control.type" v-bind="control.props" v-model="data[control.name]" :key="key"></component>
  </og-block>
  <og-button @click="save">Save</og-button>
</og-block>
</template>
<script>
export default {
  name: 'og-dynamic-form',
  props: ['name', 'controls', 'layout', 'data'],
  methods: {
    save(){
      console.log('save:', JSON.stringify(this.data));
    }
  }
}
</script>

<style lang="scss" scoped>
</style>


