import { Validator } from 'vee-validate'

export default class ValidatorWrapper {
    constructor(){
        this._validator = new Validator();
        this.errors = this._validator.errorBag;
        this._fields = {};
        this._fieldNames = [];
    }

    _setErrors(){
        for (var i = 0; i < this._fieldNames.length; i++){
            var fieldName = this._fieldNames[i];
            this._setError(fieldName);
        }
    }

    _setError(name){
        if(this.errors.has(name)){
            this._fields[name].setError(this.errors.first(name));
        } else {
            this._fields[name].setError(undefined);
        }
    }

    validateAll(){
        var fieldValidators = [];
        for (var i = 0; i < this._fieldNames.length; i++){
            var fieldName = this._fieldNames[i];
            var field = this._fields[fieldName];
            fieldValidators.push(this.validate(fieldName, field.getValue()));
        }
        return Promise.all(fieldValidators);
    }

    validate(name, value){
        return this._validator.validate(name, value).then(()=>{
            this._setError(name);
            return true;
        }).catch(() => {
            this._setError(name);
            return false;
        });
    }

    trigger(name, value){
        this.validate(name, value).catch(()=>{});
    }

    registerField(label, name, validate, getValue, setError) {
        this._fields[name] = { label: label, getValue: getValue, setError: setError };
        this._fieldNames.push(name);
        this._validator.attach(name, validate, {prettyName: label});
    }

    clear(){
        this._validator.errorBag.clear();
        var fields = this._fields;
        this._fieldNames.forEach(k => {
            fields[k].setError(undefined);
        });
    }
}


