import Vue from 'vue'
import { storiesOf } from '@storybook/vue'

storiesOf('Dropdown', module)
  .add('default',
    () => ({
    template: '<og-dropdown label="Basic" :value="field" :options="options"></og-dropdown>',
    data: () => ({
      field: undefined,
      options: [
        { text: 'Michael Scott', value: 'Michael Scott' },
        { text: 'Dwight Schrute', value: 'Dwight Schrute' },
        { text: 'Jim Halpert', value: 'Jim Halpert' },
        { text: 'Pam Beesly', value: 'Pam Beesly' },
        { text: 'Andy Bernard', value: 'Andy Bernard' }
      ]
    })
  }))
  .add('with setByText',
    () => ({
    template: '<og-dropdown label="Set By Text" :value="field" :setByText="setByText" :options="options"></og-dropdown>',
    data: () => ({
      field: undefined,
      options: [
        { text: 'Michael Scott' },
        { text: 'Dwight Schrute' },
        { text: 'Jim Halpert' },
        { text: 'Pam Beesly' },
        { text: 'Andy Bernard' }
      ],
      setByText: true
    })
  }))
  .add('with multiselect',
    () => ({
    template: '<og-dropdown keep-alive label="Multi-Select" :value="field" :options="options" :multiple="true"></og-dropdown>',
    data: () => ({
      field: undefined,
      options: [
        { text: 'Michael Scott', value: 'Michael Scott' },
        { text: 'Dwight Schrute', value: 'Dwight Schrute' },
        { text: 'Jim Halpert', value: 'Jim Halpert' },
        { text: 'Pam Beesly', value: 'Pam Beesly' },
        { text: 'Andy Bernard', value: 'Andy Bernard' }
      ]
    })
  }))
  .add('with model',
    () => ({
    template: '<og-dropdown label="Bound Model" value="field" :options="options"></og-dropdown>',
    data: () => ({
      field: 'Jim Halpert',
      options: [
        { text: 'Michael Scott', value: 'Michael Scott' },
        { text: 'Dwight Schrute', value: 'Dwight Schrute' },
        { text: 'Jim Halpert', value: 'Jim Halpert' },
        { text: 'Pam Beesly', value: 'Pam Beesly' },
        { text: 'Andy Bernard', value: 'Andy Bernard' }
      ]
    })
  }))
  .add('with og service',
    () => ({
    template: '<og-dropdown keep-alive label="Basic" value="field" :options="service"></og-dropdown>',
    data: () => ({
      field: undefined,
      service: 'application.fileTemplate.fileTemplateList'
    })
  }))


