import Vue from 'vue'
import { storiesOf } from '@storybook/vue'

storiesOf('Date Picker', module)
  .add('date',
    () => ({
    template: '<og-datepicker :date="startTime"></og-datepicker>',
    data: () => ({
      startTime: {
        time: ''
      }
    })
  }))
  .add('date time',
    () => ({
    template: '<og-datepicker :date="startTime" :options="options"></og-datepicker>',
    data: () => ({
      startTime: {
        time: ''
      },
      options: {
        type: 'min'
      }
    })
  }))
  .add('date min date',
    () => ({
    template: '<og-datepicker :date="startTime" :options="options"></og-datepicker>',
    data: () => ({
      startTime: {
        time: ''
      },
      options: {
        limit: [{
          type: 'fromto',
          from: new Date().toJSON()
        }]
      }
    })
  }))
  .add('date max date',
    () => ({
    template: '<og-datepicker :date="startTime" :options="options"></og-datepicker>',
    data: () => ({
      startTime: {
        time: ''
      },
      options: {
        limit: [{
          type: 'fromto',
          to: new Date().toJSON()
        }]
      }
    })
  }))


