<template>
  <md-whiteframe style="padding: 20px; border-left: solid 3px black">
      <og-link v-if="removable" class="remove-operation-button" icon="close" @click="removeOperation($event)"></og-link>
      <h2>Simple</h2>
      <h3 v-if="!editableTitle">{{title}}</h3>
      <og-text v-else v-model="title" :label="titleLabel"></og-text>
      <og-block layout="50 50">
        <og-text label="Module" v-model="operation.module"></og-text>
        <og-text label="Operation" v-model="operation.operation"></og-text>
      </og-block>
      <md-list>
        <md-list-item>
          <md-icon>settings</md-icon>
          <span>Input Parameters</span>
          <md-list-expand>
            <div style="margin-left: 30px">
              <og-button @click="addParam">Add Parameter</og-button>
              <div v-for="child in operation.params">
                <og-block layout="25 25 50">
                  <og-text label="Parameter" v-model="child.param"></og-text>
                  <og-text label="Value" v-model="child.value"></og-text>
                  <og-link class="remove-param-button" icon="delete" @click="removeParam(child)"></og-link>
                </og-block>
              </div>
            </div>
          </md-list-expand>
        </md-list-item>
      </md-list>
      <md-chips v-if="operation.parent" v-model="operation.tokens" md-input-placeholder="Output Tokens" style="margin-left: 7px; margin-right: 6px;">
        <template scope="chip">{{ chip.value }}</template>
      </md-chips>
  </md-whiteframe>
</template>

<script>
  import operation from './operation'
  
  export default {
    name: "og-simple-operation",
    mixins: [operation],
    methods: {
      addParam(){
        this.operation.params.push({
          param: undefined,
          value: undefined
        });
      },
      removeParam(child){
        var idx = this.operation.params.indexOf(child);
        this.operation.params.splice(idx, 1);
      }
    }
  }
</script>

<style lang="scss" scoped>
  .remove-operation-button {
    float: right;
  }
  .add-param-button * {
    display: inline;
  }
  .remove-param-button {
    text-align: left;
    margin-top: 20px;
  }
</style>


