import Vue from 'vue'
import { storiesOf } from '@storybook/vue'

storiesOf('Checkbox', module)
  .add('default',
    () => ({
    template: '<og-check name="field_name" label="Checkbox Label" v-model="checked"></og-check>',
    data: () => ({
      checked: true
    })
  }))
  .add('disabled',
    () => ({
    template: '<og-check name="field_name" label="Checkbox Label" v-model="checked" :enableEditMode="false"></og-check>',
    data: () => ({
      checked: false
    })
  }))


