<template>
    <md-switch v-model="inputValue" :name="label"  @input="input($event)" :disabled="!enableInput">{{label}}</md-switch>
</template>

<script>
    import input from '../mixins/input'
    import permissionsEdit from '../mixins/permission-edit'
    import permissionsView from '../mixins/permission-view'

    export default {
        "name": "og-switch",
        mixins: [input, permissionsEdit, permissionsView],
        created () {
            this.inputValue = this.value === undefined ? false : this.value
        },
        watch: {
            value (val) {
                if (typeof(val) === 'boolean') return
                
                // if undefined then return false
                if (val === undefined) {
                    this.inputValue = false
                    return
                }
                
                var jsonVal = JSON.parse(val.toLowerCase())
                if (typeof(jsonVal) === 'boolean') {
                    this.inputValue = jsonVal
                    return
                }

                throw "The switch requires a boolean value. Please ensure your value is a boolean"
            }
        }
    }
</script>

<style lang="scss">
.md-switch {
    padding-top: 16px;

    .md-switch-container {
        flex: initial !important;
    }
}
</style>



