<template>
    <div>
        <label class="og-text-label" :for="id">{{label}}</label>
        <textarea :id="id"></textarea>
    </div>
</template>

<script>
    import input from '../mixins/input'
    import Vue from 'vue'

    export default {
        name: "og-tinymce",
        props: {
            id: {
                type: String,
                required: true
            },
            options: Object,
            content: {
                type: String,
                default: ''
            },
            value: String,
            label: {
                type: String,
                default: ''
            }
        },
        watch: {
            options : function(newOptions, oldOptions) {
                    tinymce.remove();
                    this.init(newOptions);
            }
        },
        mounted () {
            this.init();
        },
        methods: {
            compareObject(obj1, obj2) {
                //Loop through properties in object 1
                for (var p in obj1) {
                    //Check property exists on both objects
                    if (obj1.hasOwnProperty(p) !== obj2.hasOwnProperty(p)) return false;
            
                    switch (typeof (obj1[p])) {
                        //Deep compare objects
                        case 'object':
                            if (!this.compareObject.compare(obj1[p], obj2[p])) return false;
                            break;
                        //Compare function code
                        case 'function':
                            if (typeof (obj2[p]) == 'undefined' || (p != 'compare' && obj1[p].toString() != obj2[p].toString())) return false;
                            break;
                        //Compare values
                        default:
                            if (obj1[p] != obj2[p]) return false;
                    }
                }
            
                //Check object 2 for any extra properties
                for (var p in obj2) {
                    if (typeof (obj1[p]) == 'undefined') return false;
                }
                return true;
            },
            init (opt) {
            let optionsData = opt || this.options;

            //Initial configuration
            let options = {}
            let s1 = new Function()
            let config = (editor) => {
                editor.on('NodeChange Change KeyUp', (e) => {
                    this.$emit('input', tinymce.get(this.id).getContent())
                    this.$emit('change', tinymce.get(this.id), tinymce.get(this.id).getContent())
                })
                editor.on('init', (e) => {
                    if(this.content != undefined) tinymce.get(this.id).setContent(this.content)
                    this.$emit('input', this.content)
                })
            }

            //Default configuration
            s1 = (e) => config(e)
            if(typeof optionsData == 'object'){

                options = Object.assign({}, optionsData)
                if(!optionsData.hasOwnProperty('selector')) options.selector = '#' + this.id
                if(typeof optionsData.setup == 'function'){
                    s1 = (editor) => {
                      config(editor)
                      optionsData.setup(editor)
                    }
                }

            } else options.selector = '#' + this.id

            options.setup = (editor) => s1(editor);
            Vue.nextTick(() => tinymce.init(options))
            }
        },
        beforeDestroy () {
            tinymce.execCommand('mceRemoveEditor', false, this.id)
        }
    }
</script>


