import Vue from 'vue'
import { storiesOf } from '@storybook/vue'

storiesOf('Table', module)
  .add('default',
    () => ({
    template: '<og-table :columns="columns" :rows="rows"></og-table>',
    data: () => ({
      columns: [
        { label: 'Id', field: 'Id' },
        { label: 'Name', field: 'Name' },
        { label: 'Date', field: 'Date' }
      ],
      rows: [
        { Id: '1', Name: 'Michael Scott', Date: '2017-10-21 00:00' },
        { Id: '2', Name: 'Dwight Schrute', Date: '2017-09-03 00:00' }
      ]
    })
  }))
  .add('with row callback',
    () => ({
    template: '<og-table title="With Row Callback" :columns="columns" :rows="rows"></og-table>',
    data: () => ({
      columns: [
        { 
          label: '',
          callback: (row) => {
            return '<strong style="color: #f37325;">' + row.Id + '</strong>'
          }
        },
        { label: 'Name', field: 'Name' },
        { label: 'Date', field: 'Date' }
      ],
      rows: [
        { Id: '1', Name: 'Michael Scott', Date: '2017-10-21 00:00' },
        { Id: '2', Name: 'Dwight Schrute', Date: '2017-09-03 00:00' }
      ]
    })
  }))
  .add('with row actions',
    () => ({
    template: '<og-table title="With Row Actions" :columns="columns" :rows="rows" :row-actions="rowActions" @row-action="rowAction"></og-table>',
    data: () => ({
      columns: [
        { label: 'Id', field: 'Id' },
        { label: 'Name', field: 'Name' },
        { label: 'Date', field: 'Date' },
      ],
      rowActions: [
        {
          action: 'edit',
          icon: 'mode_edit'
        }
      ],
      rows: [
        { Id: '1', Name: 'Michael Scott', Date: '2017-10-21 00:00' },
        { Id: '2', Name: 'Dwight Schrute', Date: '2017-09-03 00:00' }
      ]
    }),
    methods: {
      rowAction (row, action) {
        if (action === 'edit') {
          alert(`Edit button for row id of ${row.Id} was clicked.`)
        }
      }
    }
  }))
  .add('with default filters & table actions',
    () => ({
    template: '<og-table title="With Table Actions" filterable :columns="columns" :rows="rows" :table-actions="tableActions" @table-action="tableAction"></og-table>',
    data: () => ({
      columns: [
        { label: 'Id', field: 'Id' },
        { label: 'Name', field: 'Name' },
        { label: 'Date', field: 'Date' },
      ],
      tableActions: [
        {
          action: 'new',
          label: 'Add New Row',
          icon: 'add'
        }
      ],
      rows: [
        { Id: '1', Name: 'Michael Scott', Date: '2017-10-21 00:00' },
        { Id: '2', Name: 'Dwight Schrute', Date: '2017-09-03 00:00' }
      ]
    }),
    methods: {
      tableAction (action) {
        if (action === 'new') {
          alert(`New button for table was clicked.`)
        }
      }
    }
  }))
  .add('remove column sorting',
    () => ({
    template: '<og-table :columns="columns" :rows="rows"></og-table>',
    data: () => ({
      columns: [
        { label: 'Id', field: 'Id', sortable: false },
        { label: 'Name', field: 'Name', sortable: false },
        { label: 'Date', field: 'Date' }
      ],
      rows: [
        { Id: '1', Name: 'Michael Scott', Date: '2017-10-21 00:00' },
        { Id: '2', Name: 'Dwight Schrute', Date: '2017-09-03 00:00' }
      ]
    })
  }))
  
  .add('remove column filtering',
    () => ({
    template: '<og-table title="Remove Name Column Filter" filterable  :columns="columns" :rows="rows"></og-table>',
    data: () => ({
      columns: [
        { label: 'Id', field: 'Id' },
        { label: 'Name', field: 'Name', filterable: false },
        { label: 'Date', field: 'Date' }
      ],
      rows: [
        { Id: '1', Name: 'Michael Scott', Date: '2017-10-21 00:00' },
        { Id: '2', Name: 'Dwight Schrute', Date: '2017-09-03 00:00' }
      ]
    })
  }))


