import Vue from 'vue'
import { storiesOf } from '@storybook/vue'

storiesOf('Link', module)
  .add('default',
    () => ({
    template: '<og-link href="/">Internal Link</og-link>',
  }))
  .add('outbound link',
    () => ({
    template: '<og-link :href="url" :target="target">Outbound Link</og-link>',
    data: () => ({
      target: '_blank',
      url: 'http://www.orangegrid.com/'
    })
  }))
  .add('with icon',
    () => ({
    template: '<og-link :icon="icon" :href="url" :target="target">Link with Icon</og-link>',
    data: () => ({
      icon: 'chevron_left',
      target: '_blank',
      url: 'http://www.orangegrid.com/'
    })
  }))


