<template>
    <og-section>
        <og-block name="block1" layout="25 25 25 25">
            <og-dropdown label="Source Object" :options="options" v-model="objectFullName" name="SourceObjectType"></og-dropdown>
        </og-block>
        <og-table :columns="columns" :rows="rows" :row-actions="rowActions" :table-actions="tableActions" paginate filterable sortable @row-action="rowAction" @table-action="tableAction"></og-table>
        <form method="POST" action="/Design/DownloadString" enctype="application/x-www-form-urlencoded" ref="downloadForm">
            <input type="hidden" name="data" :value="encodedData"/>
            <input type="hidden" name="filename" :value="filename"/>
        </form>
        <og-modal name="DependencyExportProgress" title="Exporting Dependencies" :show="showExportProgress" :showCloseIcon="false">
            <og-progress></og-progress>
        </og-modal>
        <og-modal name="DependencyViewer" :show="showDependencyViewer">
            {{dependencies}}
        </og-modal>
        <og-modal name="ImportDialog" :title="rootImportObject ? rootImportObject.DisplayName + ' Import' : undefined" :show="showImportDialog" @close="importDialogClose">
            <div v-if="!selectedImport">
                <div class="error" v-if="importErrorMessage">{{importErrorMessage}}</div>
                <og-file @selected="selectedFiles"></og-file>
            </div>
            <div v-else-if="selectedImport">
                <md-tabs>
                    <md-tab id="overview" md-label="Overview">
                        <og-block layout="30 70">
                            <md-table v-if="dependencyTreeSummary && dependencyTreeSummary.dependencies && dependencyTreeSummary.dependencies.length > 0">
                                <md-table-header>
                                    <md-table-row>
                                        <md-table-head>
                                            Category
                                        </md-table-head>
                                        <md-table-head>
                                            Count
                                        </md-table-head>
                                    </md-table-row>
                                </md-table-header>
                                <md-table-row v-for="(item, key, index) in dependencyTreeSummary.dependencies" :key="key">
                                    <md-table-cell>
                                        {{item.name}}
                                    </md-table-cell>
                                    <md-table-cell>
                                        {{item.count}}
                                    </md-table-cell>
                                </md-table-row>
                            </md-table>
                            <div v-else>
                                No dependencies found.
                            </div>
                        </og-block>
                    </md-tab>
                    <md-tab id="knownObjects" md-label="Objects">
                        <og-block layout="30 70">
                            <og-tree v-if="dependencyTree" :model="dependencyTree" @item-click="itemClick"></og-tree>
                            <md-whiteframe v-if="selectedDependency">
                                <!--<div class="md-title">{{selectedDependency.name}}</div>-->
                                <md-tabs>
                                    <md-tab id="comparison" md-label="Comparison">

                                    </md-tab>
                                    <md-tab id="tokens" md-label="Tokens">
                                        <md-table v-if="selectedObjectTokens && selectedObjectTokens.length > 0">
                                            <md-table-header>
                                                <md-table-row>
                                                    <md-table-head>
                                                        Location
                                                    </md-table-head>
                                                    <md-table-head>
                                                        SQL
                                                    </md-table-head>
                                                </md-table-row>
                                            </md-table-header>
                                            <md-table-row v-for="(item, key, index) in selectedObjectTokens" :key="key">
                                                <md-table-cell>
                                                    {{item.PropertyName}}
                                                </md-table-cell>
                                                <md-table-cell>
                                                    {{item.Usage}}
                                                </md-table-cell>
                                            </md-table-row>
                                        </md-table>
                                        <div v-else>
                                            No tokens found.
                                        </div>
                                    </md-tab>
                                    <md-tab id="sql" md-label="SQL">
                                        <md-table v-if="selectedObjectSql && selectedObjectSql.length > 0">
                                            <md-table-header>
                                                <md-table-row>
                                                    <md-table-head>
                                                        Location
                                                    </md-table-head>
                                                    <md-table-head>
                                                        SQL
                                                    </md-table-head>
                                                </md-table-row>
                                            </md-table-header>
                                            <md-table-row v-for="(item, key, index) in selectedObjectSql" :key="key">
                                                <md-table-cell>
                                                    {{item.PropertyName}}
                                                </md-table-cell>
                                                <md-table-cell>
                                                    {{item.Value}}
                                                </md-table-cell>
                                            </md-table-row>
                                        </md-table>
                                        <div v-else>
                                            No SQL found.
                                        </div>
                                    </md-tab>
                                    <md-tab id="permissions" md-label="Permissions">
                                        <md-table v-if="selectedObjectPermissions && selectedObjectPermissions.length > 0">
                                            <md-table-header>
                                                <md-table-row>
                                                    <md-table-head>
                                                        Location
                                                    </md-table-head>
                                                    <md-table-head>
                                                        Permission
                                                    </md-table-head>
                                                </md-table-row>
                                            </md-table-header>
                                            <md-table-row v-for="(item, key, index) in selectedObjectPermissions" :key="key">
                                                <md-table-cell>
                                                    {{item.PropertyName}}
                                                </md-table-cell>
                                                <md-table-cell>
                                                    {{item.Value}}
                                                </md-table-cell>
                                            </md-table-row>
                                        </md-table>
                                        <div v-else>
                                            No permissions found.
                                        </div>
                                    </md-tab>
                                    <md-tab id="debug" md-label="Debug Info">
                                        Dependency:<br/>{{selectedDependency}}<br/>
                                    </md-tab>
                                </md-tabs>
                            </md-whiteframe>
                        </og-block>
                    </md-tab>
                    <md-tab id="unprocessedObjects" md-label="Debug Info">
                        {{this.unprocessedObjects}}
                    </md-tab>
                </md-tabs>
            </div>
        </og-modal>
    </og-section>
</template>

<script>
    import og from '../services/og'
    import changeCase from 'change-case'
    import http from '../services/http'
    import qs from 'qs'

    export default {
        name: "og-object-table",
        data() {
            return {
                // og-table config
                tableActions: [{
                        action: 'import',
                        label: 'Import',
                        icon: 'file_upload'
                    }],
                rowActions: [{
                    action: 'dependencies',
                    icon: 'view_module'
                },{
                    action: 'download',
                    icon: 'file_download'
                }],
                columns: undefined,
                rows: undefined,

                // og-object-table config
                objectId: undefined,
                objectFullName: undefined,
                objectService: undefined,
                options: undefined,
                dependencies: undefined,
                showExportProgress: undefined,
                showDependencyViewer: undefined,

                //ImportDialog
                showImportDialog: undefined,
                importErrorMessage: undefined,
                selectedImport: undefined,
                selectedDependency: undefined,
                unprocessedObjects: undefined,
                collectionResults: undefined
            };
        },
        watch: {
            objectFullName(value){
                this.setService(value);
                this.updateColumns();
            }
        },
        created(){
            this.updateColumns();

            og.application.objectConfiguration.moduleListEx().then(response => {
                var list = response.data;
                this.options = list.filter(i => i.Namespace).map(x => ({text: x.Name, value: x.Namespace + "." + x.Name}));
            });
        },
        computed: {
            encodedData(){
                return btoa(unescape(encodeURIComponent(JSON.stringify(this.dependencies))));
            },
            filename(){
                return this.objectName + "_" + this.objectId + ".json";
            },
            disableExport(){
                return !this.dependencies;
            },
            objectNameParts(){
                if(!this.objectFullName){
                    return undefined;
                }
                return this.objectFullName.split('.');
            },
            objectNamespace(){
                if(this.objectNameParts && this.objectNameParts.length > 0){
                    return this.objectNameParts[0];
                }
            },
            objectName(){
                if(this.objectNameParts && this.objectNameParts.length > 1){
                    return this.objectNameParts[1];
                }
            },
            rootImportObject(){
                if(!this.selectedImport){
                    return undefined;
                }

                return this.selectedImport.Dependencies.filter(d => d.Path == this.selectedImport.Root)[0];
            },
            dependencyTree(){
                if(!this.rootImportObject){
                    return undefined;
                }

                var self = this;

                var rootDependency = self.rootImportObject;

                var getName = function(namedItem){
                    return namedItem.Name + ' (#' + namedItem.Id + ')';
                };

                var tree = {name: getName(rootDependency)};

                var getCategory = function(categoryName){
                    var category = tree.children.filter(i => i.name == categoryName)[0];

                    if(!category){
                        category = {};
                        category.name = categoryName;
                        category.children = [];
                        tree.children.push(category);
                    }

                    return category;
                };

                var seenObjects = [];

                var unprocessedObjects = [];
                
                var buildTree;
                buildTree = function(dep, root){
                    if(!root.children){
                        root.children = [];
                    }

                    if(seenObjects.filter(i => i == dep).length > 0){
                        return;
                    }

                    seenObjects.push(dep);

                    if(dep != rootDependency){

                        var displayName = dep.PluralizedDisplayName;

                        if(displayName){
                            var category = getCategory(displayName);

                            category.children.push({
                                name: getName(dep),
                                source: dep
                            });
                        } else {
                            unprocessedObjects.push(dep);
                            console.log('processed a dependency that will not be added to the tree:', dep.DependencyType, dep);
                        }
                    }
                    
                    if(dep.Dependencies){
                        // process dependencies
                        dep.Dependencies.forEach(depRef => {
                            if(!depRef.Path){
                                buildTree(depRef, root);
                            } else {
                                var nextDep = self.selectedImport.Dependencies.filter(d => d.Path == depRef.Path)[0];
                                if(nextDep){
                                    buildTree(nextDep, root);
                                }  
                            }
                        });
                    }
                };

                buildTree(rootDependency, tree);

                this.unprocessedObjects = unprocessedObjects;

                return tree;
            },
            dependencyTreeSummary(){
                if(!this.dependencyTree || !this.selectedImport){
                    return;
                }

                var dependencyTree = this.dependencyTree;

                var summary = {};

                summary.dependencies = dependencyTree.children.map(c => ({name: c.name, count: c.children && c.children.length > 0 ? c.children.length : 0}));
                
                var unprocessedObjects = this.unprocessedObjects;

                var tokens = unprocessedObjects.filter(i => i.DependencyType == "TokenDependency");
                var sqls = unprocessedObjects.filter(i => i.DependencyType == "SqlDependency");
                var permissions = unprocessedObjects.filter(i => i.DependencyType == "PermissionDependency");

                summary.dependencies.push({name: "Tokens", count: tokens.length});
                summary.dependencies.push({name: "SQL", count: sqls.length});
                summary.dependencies.push({name: "Permissions", count: permissions.length});

                return summary;
            },
            selectedObjectComparison(){
                if(!this.collectionResults || !this.selectedDependency){
                    return;
                }

                // find similar objects...
                var selectedDependency = this.selectedDependency;
                var results = {};

                // as we change objects, dependencies need to be updated
                // to do this, we can make the list of dependencies dependant on 
                // the comparison list, perhaps by updating the display of the item in the list (graying out, or something)
                results.similar = collectionResults.filter(r => r.list == selectedDependency.List);
                results.recommendedMergeAction = "Import"; //import or change
                results.recommendedChangeToObject = undefined; // when 
                results.mergeAction = undefined; // import (save as new object), change to (map references in import to existing object in new org)
                results.changeToObject = undefined; // when merge action is "change", references will be updated to this object

                return results;
            },
            selectedDependencySource(){
                if(!this.selectedDependency){
                    return undefined;
                }

                return this.selectedDependency.source;
            },
            selectedObjectTokens(){
                if(!this.selectedDependencySource || !this.selectedDependencySource.Dependencies){
                    return;
                }

                return this.selectedDependencySource.Dependencies.filter(i => i.DependencyType == "TokenDependency");
            },
            selectedObjectSql(){
                if(!this.selectedDependencySource || !this.selectedDependencySource.Dependencies){
                    return;
                }

                return this.selectedDependencySource.Dependencies.filter(i => i.DependencyType == "SqlDependency");
            },
            selectedObjectPermissions(){
                if(!this.selectedDependencySource || !this.selectedDependencySource.Dependencies){
                    return;
                }

                return this.selectedDependencySource.Dependencies.filter(i => i.DependencyType == "PermissionDependency");
            }
        },
        methods: {
            rowAction(object, action){
                this[action](object);
            },
            tableAction(action){
                this[action]();
            },
            reload(){
                if(this.service){
                    this.service.search().then(response => {
                        var data = response.data;
                        var collection = data[Object.getOwnPropertyNames(data)[0]];
                        var items = collection[Object.getOwnPropertyNames(collection)[0]];
                        this.rows = items;
                    });
                }
            },
            updateColumns(){
                if(this.service){
                    this.service.meta({GroupBy: "AttributeType", Filter: "OgTableColumnAttribute"}).then(response => {
                        if(response.data.Attributes.length > 0){
                            var columnAttributes = response.data.Attributes[0].OgTableColumnAttribute;
                            this.columns = columnAttributes.map(x => ({label: x.Label, field: x.Property}));
                        }
                        this.reload();
                    });
                } else {
                    this.columns = undefined;
                    this.reload();
                }
            },
            setService(value){
                this.service = undefined;

                if(this.objectNamespace && this.objectName){
                    this.service = og[changeCase.camel(this.objectNamespace)][changeCase.camel(this.objectName)];
                }
            },
            download(object) {
                this.showExportProgress = true;
                var objectIdProperty = this.objectName + "ID";
                var params = {};
                this.objectId = object[objectIdProperty];
                params[objectIdProperty] = this.objectId;
                this.service.dependencies(params).then(response => {
                    this.dependencies = response.data;
                    this.$nextTick(()=>{
                        this.exportDependencies();
                        this.showExportProgress = false;
                    });
                });
            },
            import(){
                this.showImportDialog = true;
            },
            checkDependencies() {
                this.dependencies = undefined;
                var idName = this.objectName + "ID";
                var params = JSON.parse("{\"" + idName + "\": " + this.objectId + "}");
                this.service.dependencies(params).then(res => this.dependencies = res.data)
            },
            exportDependencies() {
                if(this.dependencies){
                    this.$refs["downloadForm"].submit();
                }
            },
            selectedFiles(fileList){
                var self = this;
                var file = fileList.length > 0 ? fileList[0] : undefined;

                if (file.name.match(new RegExp("\\.json$"))) {
                    this.importErrorMessage = undefined;
                    var reader = new FileReader();

                    reader.onload = function(e) {
                        self.selectedImport = JSON.parse(reader.result);
                        
                        var listUrls = self.selectedImport.Dependencies.map(d => d.List + '?Output=Json&DisplaySize=0').distinct();
                        var pathPromises = listUrls.map(url => http.get(url));
                        
                        Promise.all(pathPromises).then(collectionResults => {
                            console.log(collectionResults);
                            self.collectionResults = collectionResults.map(i => ({
                                    list: i.config.url,
                                    data: i.data
                                }));
                        });
                    }

                    reader.readAsText(file);	
                } else {
                    this.importErrorMessage = "Wrong file type detected.";
                }
            },
            itemClick(item){
                this.selectedDependency = item;
            },
            importDialogClose(){
                this.showImportDialog = false;
                this.selectedImport = undefined;
                this.selectedDependency = undefined;
            }
        }
    }

</script>

<style lang="scss" scoped>
</style>


