<script>
/*****************************************************
 * Notes: If you are using multiple checkboxes on a single page then you MUST
 * define the property of name so that the UI correctly applies the label. 
 * Failure to do so will result in check boxes being checked by other labels. 
 ****************************************************/ 
</script>

<template>
  <div v-show="showInput">
    <md-checkbox v-model="inputValue" :id="name" :name="name" @change="checkChanged($event)" :disabled="!enableInput">{{label}}</md-checkbox>
    <span v-show="error" class="md-error"><og-icon v-show="error" icon="error" class="md-accent"></og-icon> {{ error }}</span>
  </div>
</template>

<script>
import input from '../mixins/input'
import permissionsEdit from '../mixins/permission-edit'
import permissionsView from '../mixins/permission-view'

export default {
  name: 'og-check',
  mixins: [input, permissionsEdit, permissionsView],
  props: {
    checkedValue: {
      default: true
    },
    uncheckedValue: {
      default: false
    }
  },
  created () {
    this.inputValue = this.value
  },
  methods: {
    checkChanged(target){
      var value = target ? this.checkedValue : this.uncheckedValue;
      this.input(value);
    }
  }
}
</script>

<style lang="scss" scoped>
</style>


