/* Auth */
import OgAuth from './og-auth.vue'

/* navigation */
import OgHeader from './og-header.vue'
import OgFooter from './og-footer.vue'

const options = {
    install(Vue) {
        Vue.component('og-auth', OgAuth);
        Vue.component('og-header', OgHeader);
        Vue.component('og-footer', OgFooter);
    }
}

export default options


