<template>
  <og-simple-operation v-if="operationType === 'Operation'" :operation="operation" :title="title" :titleLabel="titleLabel" :editableTitle="editableTitle" style="margin:30px" @remove="removeOperation" removable></og-simple-operation>
  <og-batch-operation v-else-if="operationType === 'BatchOperation'" :operation="operation" :title="title" :titleLabel="titleLabel" :editableTitle="editableTitle" style="margin:30px" @remove="removeOperation" removable></og-batch-operation>
  <og-compound-operation v-else-if="operationType === 'CompoundOperation'" :operation="operation" :title="title" :titleLabel="titleLabel" :editableTitle="editableTitle" style="margin:30px" @remove="removeOperation" removable></og-compound-operation>
  <div v-else-if="operationType === 'Empty'" style="margin:30px">
    <og-dropdown :label="newOperationTypeLabel" :options="operationTypes" v-model="newOperationType"></og-dropdown>
    <og-button @click="newOperation">{{newOperationText}}</og-button>
  </div>
  <div v-else style="margin:30px">
    Unknown operation type: {{operation}}
  </div>
</template>

<script>
  import operation from './operation'

  export default {
    name: "og-operation",
    mixins: [operation],
    data() {
      return {
        operationTypes: [
            { text: 'Simple', value: 'Operation'},
            { text: 'Batch', value: 'BatchOperation'},
            { text: 'Compound', value: 'CompoundOperation'}
        ],
        newOperationType : 'Operation'
      }
    },
    props: {
      newOperationText: {
        type: String,
        default: "Add"
      },
      newOperationTypeLabel: {
        type: String,
        default: "Operation Type"
      }
    },
    computed: {
      operationType() {
        return (this.operation && this.operation.type) || 'Empty';
      }
    },
    methods: {
      newOperation($event){
        this.$emit('newOperationType', $event, this.newOperationType);
      }
    }
  }
</script>

<style lang="scss">

</style>



