<template>
	<md-table-card>
		<og-table-filter-bar 
			v-if="filterable"
			:filterable="filterable"
			:dynamicFilter="dynamicFilter"
			@change="onFilterRows"
			:tableActions="tableActions"
			@table-action="clickTableAction"
			:title="title"
			@filterClick="onfilterModal">
		</og-table-filter-bar> 

			<md-table :md-sort="column_props[0].field" :md-sort-type="sort_dir" @sort="onSort" @select="onSelect">
				<md-table-header>
					<md-table-row>
						<md-table-head v-for="(head_column, headIndex) in column_props" v-if="head_column.visible" :md-sort-by="head_column.sortable && head_column.field ? head_column.field : undefined" :key="headIndex">
							{{ head_column.label }}
						</md-table-head>
					</md-table-row>
					<md-table-row v-if="row_actions"></md-table-row>
				</md-table-header>

				<md-table-body>
					<md-table-row v-if="visible_rows.length > 0" v-for="(row, rowIndex) in visible_rows" :key="rowIndex" >
						<md-table-cell v-for="(row_column, columnIndex) in column_props" :key="columnIndex" v-if="row_column.visible">
							<span v-if="row_column.field && !row_column.callback">{{ row[row_column.field] }}</span>
							<span v-if="row_column.callback" v-html="row_column.callback(row)"></span>
							<component v-if="row_column.component" :is="row_column.component.name" :row="row" :options="row_column.component.options"></component>
						</md-table-cell>
						<md-table-cell v-if="row_actions" class="row_actions">
							<span v-for="(rowAction, rowActionKey, rowActionIndex) in row_actions" :key="rowActionKey">
								<og-link :icon="rowAction.icon" @click="clickRowAction($event, row, rowAction)">{{rowAction.label}}</og-link>&nbsp;&nbsp;
							</span>
						</md-table-cell>
					</md-table-row>
					<md-table-row v-if="visible_rows.length === 0">
						<md-table-cell :colspan="column_props.length">No Data Available.</md-table-cell>
					</md-table-row>
				</md-table-body>
			</md-table>
			
			<md-table-pagination v-if="paginate"
				:md-size="size"
				:md-total="count"
				:md-page="page"
				md-label="Rows per page"
				md-separator="of"
				:md-page-options="page_options"
				@pagination="onPagination($event)"></md-table-pagination>
				
		<og-modal v-if="filterable && dynamicFilter" :show="filter_modal" name="filter_server_modal" title="Filter" @close="closeFilter">
			<og-table-filter-server-modal
				:columns="column_props"
				@update="updateFilters"
				@close="closeFilter">
			</og-table-filter-server-modal>
		</og-modal>
	</md-table-card>
</template>

<script>
	import utilities from '../../../utilities'

	import OgTableFilterBar from './og-table-filter-bar.vue'
	import OgTableFilterServerModal from './og-table-filter-server-modal.vue'
	
	// column components
	import OgColumnAnchorLink from './og-column-anchor-link.vue'
	import OgColumnAvatar from './og-column-avatar.vue'
	import OgColumnButton from './og-column-button.vue'
	import OgColumnRouterLink from './og-column-router-link.vue'

	export default {
		props: {
			tableActions: [Array],
			rowActions: [Array],
			columns: [Object, Array],
			rows: [Object, Array],
			selected: [Object],
			selectable: {
				type: Boolean,
				default: false
			},
			filterable: {
				type: Boolean,
				default: false
			},
			paginate: {
				type: Boolean,
				default: false
			},
			filterBar: {
				type: String,
				default: 'filter-bar'
			},
			title: {
				type: String,
				default: ''
			},
			serverSideService: {
				type: Object,
				default: undefined
			}
		},
    data () {
      return {
		  	column_props: [],
				processing_rows: [],
				sort_by: 0,
				sort_dir: 'asc',
				selected_row: null,
				filter_modal: false,
				filter_text: '',
				filter_server_object: undefined,
				count: '',
				next: '',
				previous: '',
				reseterID: 0,
				size: 10,
				page: 1,
				page_options: [10, 25, 50, 100],
			}
	},
	created () {
		this.setColumnProps()
	},
	computed: {
		dynamicFilter () {
			return this.serverSideService ? true : false
		},
		sort_rows () {
			if (this.serverSideService === undefined) {
				var column = this.column_props[this.sort_by]

				if (!column || this.sort_by === null) {
					return this.processing_rows
				}
				
				return this.processing_rows.sort(function (a, b) {
					var value_a = column.callback ? column.callback(a) : a[column.field].toLowerCase()
					var value_b = column.callback ? column.callback(b) : b[column.field].toLowerCase()

					if (value_a == value_b) {
						return 0
					}

					var sort_val = value_a > value_b ? 1 : -1

					if (this.sort_dir === 'desc') {
						sort_val *= -1
					}

					return sort_val
				}.bind(this))
			} else {
				return []
			}
		},
		row_actions() {
			return utilities.mapPropertyArray(this.rowActions, s => ({action: s, icon: s}));
		},
		filter_rows () {
			if (this.serverSideService === undefined) {
				var rows = this.processing_rows;

				if(this.filterable && this.filter_text){
					var filter_words = this.filter_text.split(' ');
					
					return rows.filter(function(row){

						for(var f in filter_words){
							var filter_word = filter_words[f];

							if(typeof filter_word.toLowerCase === 'function'){
								filter_word = filter_word.toLowerCase();
							}

							var pass = false;
							// var i = 0;

							for(var i in this.column_props){
								var column_definition = this.column_props[i];
								var column_text = '';

								if(!column_definition.filterable){
									continue;
								}

								if(column_definition.field){
									column_text = row[column_definition.field];
								}else if(typeof column_definition.callback === 'function'){
									column_text = (column_definition.callback)(row);
								}else{
									continue;
								}

								if(!column_text){
									continue;
								}

								column_text = ('' + column_text + '').trim();

								if(typeof column_text.toLowerCase === 'function'){
									column_text = column_text.toLowerCase();
								}

								if(column_text.indexOf(filter_word) !== -1){
									var pass = true;
								}
							}

							return pass;
						}

						return true;
					}, this);
				}

				return rows;
			} else {
				return [];
			}
		}
	},
	asyncComputed: {
		visible_rows: {
			get () {
				// created reseter to allow us to reset the computed rows
				var reseterID = this.reseterID

				if (this.serverSideService === undefined) {
					// determine if rows are defined
					if (!this.rows || this.rows.length == 0) return new Promise((resolve, reject) => { resolve([]) })

					// create an nonreferenced copy of the array
					this.processing_rows = JSON.parse(JSON.stringify(this.rows))
					
					// filter if text is supplied
					if (this.filter_text) this.processing_rows = this.filter_rows

					// set current page 
					this.count = this.processing_rows.length

					// sort
					this.processing_rows = this.sort_rows
					
					// paging
					if(this.paginate){
						var beginning = this.size * (this.page - 1)
						this.processing_rows = this.processing_rows.slice(beginning, beginning + this.size)
					}

					return new Promise((resolve, reject) => { resolve(this.processing_rows) })
				} else {
					if (this.column_props.length <= 0) return new Promise((resolve, reject) => { resolve([]) })

					var query = this.setServerSideFilter(this.column_props) || {}
					// define base query params
					query['DisplaySize'] = this.size
					query['RecordStart'] = (this.page - 1) * this.size
					
					// set column sort and direction
					var column = this.column_props[this.sort_by].field
					if (this.sort_dir == 'asc') {
						if (query.hasOwnProperty('OrderBy-')) {
							query['OrderBy-'] = undefined
						}
						query["OrderBy"] = column
					} else {
						if (query.hasOwnProperty('OrderBy')) {
							query['OrderBy'] = undefined
						}
						query["OrderBy-"] = column
					}
					
					var self = this
					// call service with promise
					return this.serverSideService.search(query).then(res => {
						// assuming this is a ashx call. if not then we will need to update the code below.
						var returnType = res.config.url.split('/').find(x => x.endsWith('.ashx')).split('.')[0]
						var collection = res.data[Object.keys(res.data)[0]]
						res.data = collection[returnType + 'Item']

						if (collection.MetaItem) {
							var metaItem = collection.MetaItem[0]
							self.count = metaItem.Total
						} else {
							self.count = res.data[0].RecordCount
						}

						return res
					}).then(res => res.data)
				}
			},
			default: []
		}
	},
	methods: {
		clickRowAction (event, row, rowAction) {
			var shouldPerformAction = true;
			if(rowAction.confirm){
				shouldPerformAction = confirm(rowAction.confirm(row))
			}

			if(shouldPerformAction){
				this.$emit('row-action', row, rowAction.action)
			}
		},
		clickTableAction (action) {
			this.$emit('table-action', action)
		},
		onFilterRows (filter) {
			this.filter_text = filter
		},
		onPagination (e) {
			this.size = e.size
			this.page = e.page
		},
		onSelect(data) {
			this.selectedData = data
			this.$forceUpdate()
		},
		onSort(sort) {
			var field = this.column_props.find(function(c) {
				return c.field == sort.name
			})

			this.sort_by = field.id
			this.sort_dir = sort.type
		},
		setColumnProps () {
			var self = this
			var i = 0
			
			this.column_props = this.columns.map(function (column) {
				var sortable = typeof column.sortable === 'undefined' ? true : column.sortable
				sortable = column.component ? false : sortable

				if (!sortable && i == self.sort_by) {
					self.sort_by++
				}

				var filterable = typeof column.filterable === 'undefined' ? true : column.filterable
				filterable = column.component ? false : filterable
				
				return {
					id: i++,
					label: column.label || '',
					align: column.align || 'left',
					sortable: sortable,
					filterable: filterable,
					filter_text: column.filter_text || '',
					filterLabel: column.filterLabel || undefined,
					filterType: column.filterType || null,
					field: column.field || null,
					callback: column.callback || null,
					component: column.component || null,
					datasource: column.datasource || null,
					visible: column.visible == undefined ? true : column.visible
				}
			})
		},
		setServerSideFilter (column_props, clear) {
			var self = this

			if (clear || !self.filter_server_object) self.filter_server_object = {}
			column_props.forEach(column => {
				if (column.filter_text !== undefined && column.filter_text !== '') {
					if (column.filterType == Boolean) {
						self.filter_server_object[column.field] = column.filter_text ? 1 : 0
					} else if (column.filterType == 'dropdown')
						self.filter_server_object[column.field] = column.filter_text
					else // assuming string that needs like sql query
						self.filter_server_object[column.field] = column.filter_text ? '~%' + column.filter_text + '%' : undefined		
				}
			})

			return this.filter_server_object
		},
		reset() {
			this.reseterID++
		},
		updateFilters () {
			this.setServerSideFilter(this.column_props, true)
		},
		closeFilter () {
			var self = this
			this.filter_modal = false
		},
		onfilterModal () {
			this.filter_modal = true
		}
	},
	components: {
		'og-table-filter-bar': OgTableFilterBar,
		'og-table-filter-server-modal': OgTableFilterServerModal,
		'og-column-anchor-link': OgColumnAnchorLink,
		'og-column-avatar': OgColumnAvatar,
		'og-column-button': OgColumnButton,
		'og-column-router-link': OgColumnRouterLink,
	}
}
</script>

<style lang="scss">
.md-table .md-table-cell.md-has-action.row_actions .md-table-cell-container {
	justify-content: flex-end;
}

.md-table-pagination {
	 @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
    /* IE10+ CSS styles go here */
      -ms-flex: 1 1 auto !important;
      flex: 1 1 auto !important;
    }
}
</style>


