<script>
/*
  Type: dropdown list (select with options)
  Validators: If set on parent container/object this will pass down and validate accordingly.
  Props:
    Label: inherited value from input mixin for the label to be displayed.
    Name: inherited value from input mixin for the name of the field.
    Value: inherited value from input mixin for the model field that is being updated.
    Options: the list of items that are displayed to the client. The field accepts a variety
             of types and they will be handled in different ways:
        
        1. An array of key value pairs (i.e. [{ key: "key", value: "value" }] )
        2. A string which will be split on a '.' It is expected that you pass {service}.{action}.{method}
          to get the service from the 'og' object. An example would be to pass 'application.objectConfiguration.methodList'
          to get the list of methods available.
        3. An object (example shown below) where you pass the options split up within the object. and 
          optionally pass in parameters for the query. An example use of the parameters option would be
          to take the value from a moduleList dropdown and pass the selected value as a parameter (using a 
          computed value option) to the "application.objectConfiguration.methodList" service call. Keep in 
          mind that if the service has required fields they should be passed in the parameters.
              Example:
                {
                  service: '{service}',
                  action: '{action}',
                  method: '{method}',
                  params: { //optional
                    key: 'value',
                    key: 'value
                  }
                }
*/
</script>

<template>
  <md-input-container v-show="showInput">
      <label :for="name">{{label}}</label>
      <md-select :name="name" :id="name" @input="input($event)" v-model="inputValue" :ref="parentRef" :disabled="!enableInput" :multiple="multiple">
        <md-option @selected="clearOption" v-if="!multiple && hasSelection" class="clearSelection" value=""><small>Clear Selection</small></md-option>
        <md-option v-for="(option, key, index) in listOptions" :value="option.value" :key="key">{{option.text}}</md-option>
      </md-select>
  </md-input-container>
</template>

<script>
import og from '../../services/og'
import input from '../mixins/input'
import permissionsEdit from '../mixins/permission-edit'
import permissionsView from '../mixins/permission-view'

export default {
  name: 'og-dropdown',
  mixins: [input, permissionsEdit, permissionsView],
  props: ['options', 'multiple', 'setByText'],
  data () {
    return {
      listOptions: [],
      parentRef: 'parent' + Math.random()
    }
  },
  watch: {
    options (val) {
      this.getData()
    },
    value (val) {
      // if not setbytext then return. the value has been set
      if (!this.setByText) return
      // if is setbytext and value is undefined then return empty array
      if (!this.value) {
        this.inputValue = []
        return
      }
      // if is setbytext and is an array with a value then return. the value has been set
      if (this.value && Array.isArray(this.value)) return

      var self = this
      var items = this.value.split(',')
      var ids = []

      items.forEach(i => {
        i = i.trim()
        var option = self.listOptions.find(lo => lo.text == i)
        if (!option) throw "The dropdown did not contain " + i
        
        ids.push(option.value)
      })

      this.inputValue = ids
    }
  },
  created () {
    this.getData()
    // set value to empty array if it is multiple and value is undefined
    this.inputValue = (this.multiple && this.value === undefined) ? [] : this.value
  },
  computed: {
    hasSelection () {
      if (this.listOptions && !this.listOptions.length > 0) return false;
      
      var parent = this.$refs[this.parentRef];
      if (!parent) return false;
      
      // fix for selected text not clearing the clear select text.
      if (parent.selectedText === 'Clear Selection') parent.selectedText = undefined

      return parent.selectedValue != undefined
    }
  },
  methods: {
    clearOption() {
      this.$refs[this.parentRef].selectedText = undefined
      this.$refs[this.parentRef].selectedValue = undefined
      this.$emit('input', null)
    },
    getData() {
      if (!this.options) return
      
      if (Array.isArray(this.options)) {
        this.listOptions = this.options
      } else if (typeof this.options === 'string') {
        var actions = this.options.split('.')
        if (actions.length != 3) throw 'The value options is not properly set. Requires "{service}.{handlername}.{method}".'

        og[actions[0]][actions[1]][actions[2]]().then(x => {
          this.listOptions = x
        })
      } else if (typeof this.options === 'object') {
        og[options.service][options.action][options.method](options.params).then(x => {
          this.listOptions = x
        })
      }
    }
  }
}
</script>

<style lang="scss">
  .clearSelection {
    .md-list-item-container {
      justify-content: center;
    }
  }
</style>



