import AbstractObject from '../abstract-object'

export default class SystemPermission extends AbstractObject {
    constructor() {
        super("/Security/SystemPermission.ashx/"
        );
    }

    _checkUserPermissions (permissionArray, permission, cb) {
        var hasPermission = permissionArray.includes(permission) || permissionArray.includes('AdministratorPermissions')
        cb(hasPermission)
    }

    userPermissions (params) {
        return this._get('UserPermissions', params)
            .then(res => {
                res.data = res.data.SystemPermissionCollection.SystemPermissionItem
                return res
            })
            .then(res => res.data.map(x => x.SystemFunction))
    }

    userHasPermission (permission, cb) {
        // default variables
        var perms
        var permissionsStorageKey = 'ogup'
        var permissionsStorageExpirationKey = 'ogupexp'
        var permissionArray = []
        var permsExpiration
        var now = (new Date()).getTime()
        var permTimeoutMs = 3600000 //1 hour

        try {
            permsExpiration = localStorage.getItem(permissionsStorageExpirationKey)
            if (permsExpiration && permsExpiration > now) {
                perms = localStorage.getItem(permissionsStorageKey)
            }
        }
        catch (e) {
            console.log('localstorage not supported (get)')
        }

        if (!perms) {
            permsExpiration = (new Date()).getTime() + permTimeoutMs;

            try {
                localStorage.setItem(permissionsStorageExpirationKey, permsExpiration)
            } 
            catch (e) {
                console.log('localstorage not supported (set expiration)');
            }

            this.userPermissions().then(res => {
                permissionArray = res

                try {
                    localStorage.setItem(permissionsStorageKey, permissionArray.toString())
                } 
                catch (e) {
                    console.log('localstorage not supported (set perms)');
                }

                this._checkUserPermissions(permissionArray, permission, cb)
            })
        } else {
            permissionArray = perms.split(',')
            this._checkUserPermissions(permissionArray, permission, cb)
        }
    }
}


