<template>
  <md-whiteframe style="padding: 20px; border-left: solid 3px gray">
    <og-link v-if="removable" class="remove-operation-button" icon="close" @click="removeOperation($event)"></og-link>
    <h2>Compound</h2>
    <h3 v-if="!editableTitle">{{title}}</h3>
    <og-text v-else v-model="title" :label="titleLabel"></og-text>
    <og-block layout="50 50">
      <og-text label="Module" v-model="operation.module"></og-text>
      <og-text label="Operation" v-model="operation.operation"></og-text>
    </og-block>
    <og-button @click="newChildOperation">Add Child Operation</og-button>
    <og-operation v-for="(child, index) in operation.children" :operation="child" :title="child.param" titleLabel="Parameter" editableTitle :key="child.key" @remove="removeChildOperation(index)" @newOperationType="($event, operationType) => newChildOperationType(operationType, index)" removeable></og-operation>
    <!--<md-chips v-if="operation.parent" v-model="operation.tokens" md-input-placeholder="Output Tokens" style="margin-left: 7px; margin-right: 26px;">
      <template scope="chip">{{ chip.value }}</template>
    </md-chips>-->
  </md-whiteframe>
</template>

<script>
  import operation from './operation'
  import utilities from '../../utilities'

  export default {
    name: "og-compound-operation",
    mixins: [operation],
    props: {
      removable: Boolean
    },
    methods: {
      newChildOperation(){
        this.operation.children.push(this.createEmptyOperationModel());
      },
      newChildOperationType(operationType, index){
        var operation;

        if(operationType === 'BatchOperation'){
            operation = this.createBatchOperationModel();
        } else if(operationType === 'CompoundOperation'){
            operation = this.createCompoundOperationModel();
        } else if(operationType === 'Operation'){
            operation = this.createSimpleOperationModel();
        }

        this.operation.children.splice(index, 1, operation);
      },
      removeChildOperation(index){
        this.operation.children.splice(index, 1);
      }
    }
  }
</script>

<style lang="scss" scoped>
  .remove-operation-button {
    float: right;
  }
</style>


