<template>
    <og-textarea
        :name="name"
        :label="label"
        v-model="inputValue"
        :icon="icon"
        :inputType="inputType"
        :pattern="pattern"
        :title="title"
        @input="update"></og-textarea>
</template>

<script>
    import mixin from './mixin.js'

    export default {
        name: "og-field-textarea",
        mixins: [ mixin ],
        created () {
            if (this.fieldDefinition.MinLength) {
                this.addValidation('min:' + this.fieldDefinition.MinLength)
            }

            if (this.fieldDefinition.MaxLength) {
                this.addValidation('max:' + this.fieldDefinition.MaxLength)
            }
        }
    }
</script>

<style lang="scss">

</style>



