<template>
  <og-link :href="url" @click="click()" :icon="options.icon">{{optionslabel}}</og-link>
</template>

<script>
  export default {
    props: ['row', 'options'],
    methods: {
      click(){
        if(this.options.click){
          this.options.click(this.row);
        }
      }
    },
    computed: {
      label () {
        if (typeof this.options.label == 'function') {
          return this.options.label(this.row)
        }

        return this.options.label
      },
      url () {
        var url;

        if(this.options.baseUrl && this.options.model){
          url = this.options.baseUrl + this.row[this.options.model];
        } else {
          url = "#"
        }

        return url;
      }
    }
  }
</script>

<style scoped>
  a:hover {
    text-decoration: none !important;
  }
</style>


