import AbstractObject from '../abstract-object'
import utilities from '../../../utilities'

export default class Contact extends AbstractObject {
    constructor() {
        super("/Contact/Contact.ashx/" // baseUrl
        );
    }

    _map(params) {
        return this.search(params).then(res => {
            res.data = res.data.ContactCollection.ContactItem
            return res
        })
    }

    contactList(params){
        return this._map(params).then(res => res.data
        .filter((o) => o.Email)
        .sort(utilities.dynamicSortMultiple('FirstName', 'LastName'))
        .map(x => ({ value: x.ContactID, text: x.FirstName + ' ' + x.LastName + ' - ' + x.Email })))
    }
   
    teamList(param) {
        return this._get("TeamList", params)
    }
}



