import AbstractObject from '../abstract-object'

export default class ObjectConfiguration extends AbstractObject {
    constructor() {
        super("/Application/ObjectConfiguration.ashx/"
        );
    }

    _mapModuleList(params) {
        return this._get("ModuleList", params).then(res => {
            res.data = res.data.ModuleCollection.ModuleItem.sort();
            return res
        })
    }

    _mapModuleListEx(params) {
        return this._get("ModuleListEx", params).then(res => { 
            res.data = res.data.ModuleCollection.ModuleItem 
            return res
        })
    }

    _mapOperationListConfig(params) {
        // param example
        // { module: moduleName, rt: returnType }
        return this._get("OperationListConfig", params).then(res => {
            res.data = res.data.OperationCollection.OperationItem
            return res
        })
    }
   
    moduleList(params) {
        return this._mapModuleList(params).then(res => res.data.map(x => ({ text: x, value: x })))
    }

    moduleListEx(params) {
        return this._mapModuleListEx(params);
    }

    operationListConfig(params) {
        return this._mapOperationListConfig(params).then(res => res.data.map(x => ({ text: x, value: x })))
    }

    statementEdit(object, params) {
        return this._post("StatementEdit", params, object);
    }
}


