import Vue from 'vue'
import { storiesOf } from '@storybook/vue'

import { action } from '@storybook/addon-actions'
import { addonNotes } from '@storybook/addon-notes'

storiesOf('Button', module)
  .add('default', 
    () => ({
    template: '<og-button></og-button>',
  }))
  .add('with action', () => ({
    template: '<og-button @click="clicker">Clicks: {{clickCount}}</og-button>',
    data: () => ({
      clickCount: 0
    }),
    methods: {
      clicker() {
        this.clickCount++;

        if (this.clickCount >= 3) {
          action()
          console.log('should have action')
        }
      }
    }
  }))
  .add('override text', () => ({
    template: '<og-button>Name the button</og-button>'
  }))
  .add('disabled', () => ({
    template: '<og-button :disabled="disabled"></og-button>',
    data: () => ({
      disabled: true
    })
  }))  
  .add('with icon', () => ({
    template: '<og-button :icon="iconValue">Add</og-button>',
    data: () => ({
      iconValue: 'add'
    })
  }))
  .add('icon only', () => ({
    template: '<og-button :icon="iconValue" :hide-label="hide"></og-button>',
    data: () => ({
      hide: true,
      iconValue: 'chevron_right'
    })
  }))
  .add('custom css class(es)', () => ({
    template: '<og-button button-class="md-primary"></og-button>'
  }))
  .add('flat button', () => ({
    template: '<og-button :button-raised="raisedBtn"></og-button>',
    data: () => ({
      raisedBtn: false
    })
  }))


