import Vue from 'vue'
import { storiesOf } from '@storybook/vue'

storiesOf('Textarea', module)
  .add('default',
    () => ({
    template: '<og-textarea name="field_name" label="Default Field" v-model="fieldValue"></og-textarea>',
    data: () => ({
      fieldValue: ''
    }),
  }))
  .add('with value',
    () => ({
    template: '<og-textarea name="field_name" label="With Values" v-model="fieldValue"></og-textarea>',
    data: () => ({
      fieldValue: 'Lorem Ipsum\r\nTest Sentence.\r\nTest Sentence.\r\nTest Sentence.\r\nTest Sentence.\r\nTest Sentence.'
    }),
  }))
  .add('disabled',
    () => ({
    template: '<og-textarea name="field_name" label="Disabled" v-model="fieldValue" :enableEditMode="false"></og-textarea>',
    data: () => ({
      fieldValue: 'Lorem Ipsum\r\nTest Sentence.\r\nTest Sentence.\r\nTest Sentence.\r\nTest Sentence.\r\nTest Sentence.'
    }),
  }))


