<template>
  <transition name="modal">
    <md-dialog tabindex="0" :ref="name" @close="onClose">
      <md-dialog-title class="og-flex-container" v-if="showHeader">
        <span>{{title}}</span> <small v-if="subhead" class="md-subhead">{{ subhead }}</small>
        <span class="og-flex"></span>
        <og-link class="modal-close" v-if="showCloseIcon" icon="close" @click="closeDialog(name)"></og-link>
      </md-dialog-title>
      <md-dialog-content>
        <slot></slot>
      </md-dialog-content>
      <md-dialog-actions v-if="showFooter">
        <slot name="footer"></slot>
      </md-dialog-actions>
    </md-dialog>
  </transition>
</template>

<script>
export default {
  data(){
    return {
      isClosed: true
    }
  },
	props: {
    name: {
      type: String
    },
    show: {
      type: Boolean,
      default: false
    },
    showCloseIcon: {
      type: Boolean,
      default: true
    },
    size: {
      type: String,
      default: 'medium'
    },
    title: {
      type: String,
      default: undefined
    },
    subhead: {
      type: String,
      default: undefined
    }
  },
  watch: {
    show: function(value) {
      if (value) {
        this.openDialog(this.name);
      } else {
        this.closeDialog(this.name);
      }
    }
  },
  computed: {
    showHeader() {
      return this.title || this.showCloseIcon
    },
    showFooter() {
      return this.$slots.footer ? true : false
    }
  },
  methods: {
    openDialog(ref) {
      if(this.isClosed){
        this.isClosed = false;
        this.$refs[ref].open(); 
      }
    },
    closeDialog(ref) {
      if(!this.isClosed){
        this.isClosed = true;
        this.$refs[ref].close();
      }
    },
    onClose(type) {
      this.$emit('close');
    }
  }
}
</script>

<style lang="scss">
.md-dialog {
  width: 100%;
  
  .md-subhead {
    color: rgb(150, 150, 150);
    padding-left: 10px;
  }
}

.og-flex-container {
  display: flex;
}

.og-flex {
  -webkit-flex: 1;
  flex: 1;
}
</style>


