<template>
    <md-list-item :class="{'child-node': isChild, 'selected-node': isSelected}" v-if="model" md-expand-multiple>
        <div v-if="isFolder" class="bold">
            <span>{{getName}}</span>
        </div>
        <div v-if="!isFolder" class="clickable-node" @click="itemClick(model)">
          <og-link v-if="getUrl" :href="getUrl" :target="getTarget">{{getName}}</og-link>
          <span v-else>{{getName}}</span>
        </div>
        <md-list-expand v-if="isFolder">
          <md-list md-dense>
              <md-list-item v-if="isTable">
                <og-table :columns="getColumns" :rows="getChildren" paginate sortable filterable></og-table>
              </md-list-item>
              <og-tree-item v-else is-child v-for="item in getChildren" :model="item" :name="name" :children="children" :url="url" :target="target" :selectedItem="selectedItem" @item-click="itemClick" :key="guid()"></og-tree-item>
          </md-list>
        </md-list-expand>
    </md-list-item>
</template>

<script>
  import utilities from '../../../utilities'

  export default {
  data(){
    return {
      columns: [{
        label: 'Name',
        callback(row){
          if(!row.url){
            return row.name;
          }
          return `<a href="${row.url}">${row.name}</a>`;
        }
      }]
    }
  },
  props: {
    model: Object,
    children: Function,
    name: Function,
    url: Function,
    target: Function,
    isChild: Boolean,
    selectedItem: Object
  },
  computed: {
    isFolder() {
      var children = this.children(this.model);
      return children && children.length;
    },
    isSelected() {
      return !this.isFolder && this.selectedItem == this.model;
    },
    isTable() {
      return this.model.display && this.model.display.toLowerCase() === 'table';
    },
    getChildren(){
      return this.children(this.model);
    },
    getName(){
      return this.name(this.model);
    },
    getUrl(){
      return this.url(this.model);
    },
    getColumns(){
      return this.model.columns ? this.model.columns : this.columns;
    },
    getTarget(){
      return this.target(this.model);
    }
  },
  methods: {
    itemClick(item) {
      this.$emit('item-click', item);
    },
    guid(){
      return utilities.guid();
    }
  }
}
</script>

<style scoped>
  .bold {
    font-weight: bold;
  }
  .selected-node {
    border-bottom: solid 3px red;
  }
  .clickable-node {
    cursor: pointer
  }
  .child-node {
    margin-left: 15px;
  }
  .md-list-item-container .md-card {
    width: 100%
  }
</style>


