<template>
  <og-router-link :href="url" :icon="options.icon">{{options.label}}</og-router-link>
</template>

<script>
  export default {
    props: ['row', 'options'],
    computed: {
      label () {
        if (typeof this.options.label == 'function') {
          return this.options.label(this.row)
        }

        return this.options.label
      },
      url () {
        return this.options.baseUrl + this.row[this.options.model]
      }
    }
  }
</script>


