/* layout components */
import OgBlock from './primitives/og-block.vue'
import OgDynamicForm from './primitives/og-dynamic-form.vue'
import OgModal from './primitives/og-modal.vue'
import OgPanel from './primitives/og-panel.vue'
import OgSection from './primitives/og-section.vue'

/* controls */
import OgBackLink from './og-back-link.vue'
import OgCompare from './og-compare.vue'
import OgObjectTable from './og-object-table.vue'
import OgProgress from './og-progress.vue'
import OgPropertyGrid from './og-property-grid.vue'

import OgButton from './primitives/og-button.vue'
import OgCheck from './primitives/og-check.vue'
import OgCodeEditor from './primitives/og-code-editor.vue'
import OgDatePicker from './primitives/og-datepicker.vue'
import OgDropdown from './primitives/og-dropdown.vue'
import OgEditor from './primitives/og-editor.vue'
import OgFieldDefintion from './og-field-definition/og-field-definition.vue'
import OgFile from './primitives/og-file.vue'
import OgHidden from './primitives/og-hidden.vue'
import OgIcon from './primitives/og-icon.vue'
import OgLink from './primitives/og-link.vue'
import OgRadio from './primitives/og-radio.vue'
import OgRouterLink from './primitives/og-router-link.vue'
import OgSpinner from './primitives/og-spinner.vue'
import OgSwitch from './primitives/og-switch.vue'
import OgTable from './primitives/og-table/og-table.vue'
import OgText from './primitives/og-text.vue'
import OgTextArea from './primitives/og-textarea.vue'
import OgTree from './primitives/og-tree/og-tree.vue'
import OgTreeItem from './primitives/og-tree/og-tree-item.vue'
import OgTinymce from './primitives/og-tinymce.vue'

import OgOperation from './og-operation/og-operation.vue'
import OgSimpleOperation from './og-operation/og-simple-operation.vue'
import OgBatchOperation from './og-operation/og-batch-operation.vue'
import OgCompoundOperation from './og-operation/og-compound-operation.vue'

import OgUserFinder from './og-user-finder.vue'
import OgUserList from './og-user-list.vue'

const options = {
    install(Vue) {
        Vue.component('og-dynamic-form', OgDynamicForm);
        Vue.component('og-block', OgBlock);
        Vue.component('og-modal', OgModal);
        Vue.component('og-panel', OgPanel);
        Vue.component('og-section', OgSection);

        Vue.component('og-back-link', OgBackLink);
        Vue.component('og-button', OgButton);
        Vue.component('og-check', OgCheck);
        Vue.component('og-code-editor', OgCodeEditor);
        Vue.component('og-compare', OgCompare);
        Vue.component('og-datepicker', OgDatePicker)
        Vue.component('og-dropdown', OgDropdown);
        Vue.component('og-editor', OgEditor);
        Vue.component('og-field-definition', OgFieldDefintion);
        Vue.component('og-file', OgFile);
        Vue.component('og-hidden', OgHidden);
        Vue.component('og-icon', OgIcon);
        Vue.component('og-link', OgLink);
        Vue.component('og-object-table', OgObjectTable);
        Vue.component('og-progress', OgProgress);
        Vue.component('og-property-grid', OgPropertyGrid);
        Vue.component('og-radio', OgRadio);
        Vue.component('og-router-link', OgRouterLink);
        Vue.component('og-spinner', OgSpinner);
        Vue.component('og-switch', OgSwitch);
        Vue.component('og-table', OgTable);
        Vue.component('og-text', OgText);
        Vue.component('og-textarea', OgTextArea)
        Vue.component('og-tree', OgTree);
        Vue.component('og-tree-item', OgTreeItem);
        Vue.component('og-tinymce', OgTinymce);
        
        Vue.component('og-operation', OgOperation);
        Vue.component('og-simple-operation', OgSimpleOperation);
        Vue.component('og-batch-operation', OgBatchOperation);
        Vue.component('og-compound-operation', OgCompoundOperation);

        Vue.component('og-user-list', OgUserList)
        Vue.component('og-user-finder', OgUserFinder)
    }
};

export default options


