import http from '../http'
import qs from 'qs'
import utilities from '../../utilities'

export default class AbstractObject {
    constructor(baseUrl, idField){
        this.baseUrl = baseUrl;
    }

    _prepareForOperation(object) {
        var objectForOperation = utilities.clone(object);

        if(object.getIdFieldName){
            var idFieldName = object.getIdFieldName();
            objectForOperation.ID = objectForOperation[idFieldName];
            objectForOperation[idFieldName] = undefined;
        }

        return objectForOperation;
    }

    _queryStringify(parameters){
        var params = parameters || [];
        if(!params.Output){
            params.Output = "Json";
        }

        return qs.stringify(params);
    }

    _get(method, params) {
        
        return http.get(this.baseUrl + method + "?" + this._queryStringify(params));
    }

    _post(method, params, object){
        return http.post(this.baseUrl + method + "?" + this._queryStringify(params), object);
    }

    dependencies(params){
        return this._get("Dependencies", params);
    }

    meta(params){
        return this._get("Meta", params);
    }

    list(params) {
        return this._get("List", params);
    }

    remove(object, params){
        var objectToRemove = this._prepareForOperation(object);
        return this._post("Delete", params, objectToRemove);
    }

    save(object, params){
        var objectToSave = this._prepareForOperation(object);
        return this._post("Save", params, objectToSave);
    }

    search(params){
        return this._get("Search", params);
    }

    summary(params){
        return this._get("Summary", params);
    }
}


