import changeCase from 'change-case'
import emptyClass from './empty-class'

const ext = ".js";
const exports = {};

function requireAll(r) {
    r.keys().forEach(key => {
        // remove '.js' and leading './'
        var path = key.substring(0, key.length - ext.length).substring(2, key.length)

        var paths = path.split('/')
        var moduleName = changeCase.camel(paths[paths.length - 1])

        if(moduleName !== "index" && moduleName !== "abstractObject" && moduleName != "emptyClass"){
            var classHierarchy = exports
            for (var i = 0; i < (paths.length - 1); i++) {
                var className = changeCase.camel(paths[i])
                if (!classHierarchy[className]) {
                    classHierarchy[className] = new emptyClass()
                }

                classHierarchy = classHierarchy[className]
            }

            var ModuleClass = r(key).default;

            if(typeof ModuleClass === 'function'){
                classHierarchy[moduleName] = new ModuleClass()
            }
        }
    })
}
requireAll(require.context('./', true, /\.js$/))

export default exports


