import AbstractObject from '../abstract-object'

export default class State extends AbstractObject {
    constructor() {
        super("/Contact/State.ashx/"
        );
    }

    _map(params) {
        return this.list(params).then(res => {
            res.data = res.data.StateCollection.StateItem
            return res
        })
    }
        
    stateList(params) {
        return this._map(params).then(res => res.data.map(x => ({ text: x.StateName, value: x.State })))
    }
}


