import Vue from 'vue'
import { storiesOf } from '@storybook/vue'

storiesOf('Radio', module)
  .add('default',
    () => ({
    template: '<og-radio :options="options"></og-radio>',
    data: () => ({
      options: [
        { text: 'one', value: 'one' },
        { text: 'two', value: 'two' },
        { text: 'three', value: 'three' }
      ]
    })
  }))
  .add('with event',
    () => ({
    template: '<og-radio v-model="selected" @input="updated($event)" :options="options"></og-radio>',
    data: () => ({
      options: [
        { text: 'one', value: 'one' },
        { text: 'two', value: 'two' },
        { text: 'three', value: 'three' }
      ],
      selected: 'two'
    }),
    methods: {
      updated(item) {
        alert(item)
      }
    }
  }))
  .add('disabled',
    () => ({
    template: '<og-radio :enableEditMode="false" :options="options"></og-radio>',
    data: () => ({
      options: [
        { text: 'one', value: 'one' },
        { text: 'two', value: 'two' },
        { text: 'three', value: 'three' }
      ]
    })
  }))


