<template>
  <md-input-container>
    <label>{{ label }}</label>
    <md-file :id="name" :name="name" @input="input($event)" @selected="selectedFile($event)" :accept="accept" :multiple="multiple"></md-file>
  </md-input-container>
</template>

<script>
  /*****
  og.File - Your upload component.
  Description: Upon selecting a file this component will automatically upload the content to the object, objectId via the
    og.attachment.attachment.uploadChunk(parameters, {form data object}) via "/Attachment/Attachment.ashx/UploadChunk"
    see the service for additional parameters that can be pased.
  ******/

  import attachment from '../../services/og/attachment/attachment'
  import input from '../mixins/input'
  // import permissionsEdit from './mixins/permission-edit'
  // import permissionsView from './mixins/permission-view'

  export default {
    name: 'og-file',
    //mixins: [input, permissionsEdit, permissionsView],
    mixins: [input],
    props: {
      accept: {
        type: String
      },
      multiple: {
        type: Boolean,
        default: false
      },
      object: {
        type: String
      },
      objectId: {
        type: Number,
        default: 0
      },
      pathUrl: {
        type: String
      }
    },
    methods: {
      selectedFile (val) {
        // Bubble up selected file list.
        this.$emit('selected', val);
      }
    }
  }
</script>


