import Vue from 'vue'
import { storiesOf } from '@storybook/vue'

storiesOf('Tiny MCE Code Editor', module)
  .add('default',
    () => ({
    template: '<og-tinymce id="default" name="field_name" label="Code Editor Label" v-model="fieldValue"></og-code-editor>',
    data: () => ({
      fieldValue: '<html>\r\n<body>\r\n<strong>Test html body</strong>\r\n</body>\r\n</html>'
    })
  }))


